import { createCustomElement } from '@servicenow/ui-core';
import noop from 'lodash/noop';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import renderTimelineBody from './view';
import styles from './styles.scss';
import { actionHandlers } from '../actions';
import rtlBehavior from '@servicenow/behavior-rtl';
import { DEFAULT_DRAWING_LINK_DATA } from '../constants';
import eventHandlers from './eventHandlers';
import { timelineBodyActions } from './actions/timelineBodyActions';

createCustomElement('sn-gantt-timeline-body', {
	renderer: {
		type: snabbdom
	},
	view: renderTimelineBody,
	styles,
	initialState: {
		movingTaskInfo: null,
		activeTaskId: null,
		drawingLinkData: DEFAULT_DRAWING_LINK_DATA,
		dependencyLinePaths: {},
		textTypeColumnName: null
	},
	properties: {
		positionMaps: {
			default: {}
		},
		config: {
			default: null
		},
		timelineData: {
			default: null
		},
		columns: {
			default: null
		},
		selectedCell: {
			default: null
		},
		bodyWrapperRef: {
			default: null
		},
		visibleTasks: {
			default: []
		},
		links: {
			default: []
		},
		baselineStatus: {
			default: [false, false]
		},
		tree: {
			default: {}
		},
		isScrollingVertically: {
			default: false
		},
		syncScroll: {
			default: noop
		},
		setRef: {
			default: noop
		},
		bottomScaleData: {
			default: {}
		}
	},
	behaviors: [{ behavior: rtlBehavior }],
	eventHandlers: eventHandlers,
	actionHandlers: {
		...actionHandlers,
		...timelineBodyActions
	}
});
