// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';

const renderBarHoverTooltip = ({
	text,
	fields,
	index,
	left,
	height,
	width,
	scrollTop,
	rowHeight
}) => {
	const top = index * rowHeight;
	const bottom = top - scrollTop < height;
	const tooltipStyle = {
		transform: `translate(${
			left
		}px, ${
			bottom ? top + rowHeight : top - height
		}px)`,
		width: `${width}px`,
		height: `${height}px`
	};


	return (
		<div className="tooltip"
			style={tooltipStyle}
		>
			<now-heading label={text} variant="title-primary" wont-wrap/>
			{fields.length > 0
				? fields.map(({ label, value }) => {
					return (
						<div className="tooltip-content">
							<div className="tooltip-row"><span>{label}:</span>{value}</div>
						</div>
					);
				})
				: <Fragment/>
			}
		</div>
	);
};

export default renderBarHoverTooltip;
