import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import renderLinks from './links';
import styles from './linkStyles.scss';
import rtlBehavior from '@servicenow/behavior-rtl';
import { commonTimelineLinkEffect } from './effect';

createCustomElement('sn-gantt-timeline-links', {
	renderer: {
		type: snabbdom
	},
	behaviors: [{ behavior: rtlBehavior }],
	view: (state) => renderLinks(state.properties),
	properties: {
		totalWidth: {
			default: null
		},
		dependencyLinePaths: {
			default: {}
		},
		timelineData: {
			default: null
		},
		config: {
			default: null
		},
		displayDependencyLines: {
			default: true
		}
	},
	styles,
	eventHandlers: [{
		events: ['click', 'mouseover', 'mouseout', 'contextmenu'],
		effect: commonTimelineLinkEffect
	}]
});
