import { getTreeUtils } from '@devsnc/sn-datagrid';
import {
	DEPENDENCY_LINE_OFFSET,
	TASK_TYPES,
	LINKS,
	MILESTONE_TASK_DIMENSION_MULTIPLIER_EXCESS_HALF
} from '../constants';
import isEmpty from 'lodash/isEmpty';
import { isVisible } from './TaskManager';

const { MILESTONE } = TASK_TYPES;
const {
	FINISH_TO_START,
	START_TO_START,
	FINISH_TO_FINISH,
	START_TO_FINISH
} = LINKS;

const getStartX = (point, isTargetMilestone) => isTargetMilestone
	? point.left - MILESTONE_TASK_DIMENSION_MULTIPLIER_EXCESS_HALF * point.width
	: point.left;

const getEndX = (point, isTargetMilestone) => isTargetMilestone
	? point.left + point.width + MILESTONE_TASK_DIMENSION_MULTIPLIER_EXCESS_HALF * point.width
	: point.left + point.width;

const moveTo = ([x, y]) => `M ${x} ${y} `;
const lineTo = ([x, y]) => `L ${x} ${y} `;
const curveTo = (bz1, bz2, endPt) => `C ${bz1[0]} ${bz1[1]} ${bz2[0]} ${bz2[1]} ${endPt[0]} ${endPt[1]} `;

const getPtOnLineByRatio = (startPt, endPt, ratio) => [
	startPt[0] + (endPt[0] - startPt[0]) * ratio,
	startPt[1] + (endPt[1] - startPt[1]) * ratio
];
// This will only work with points with one common axis, as required in dependencies
const getPtOnLineByUnits = (startPt, endPt, units) => [
	startPt[0] + Math.sign(endPt[0] - startPt[0]) * units,
	startPt[1] + Math.sign(endPt[1] - startPt[1]) * units
];

/**
 * This method draws a curve between three points by
 * 1) finding the curve start/end points
 * 2) drawing a line till curve start
 * 3) drawing a curve till curve end
 * If radius is 0, there is no curve
 */
export const turnThrough = (prevPt, currPt, nextPt, radius = 0) => {
	if (!radius) {
		return `${lineTo(currPt)}`;
	}
	const start = getPtOnLineByUnits(currPt, prevPt, radius),
		end = getPtOnLineByUnits(currPt, nextPt, radius),
		bzStart = getPtOnLineByRatio(start, currPt, 0.5),
		bzEnd = getPtOnLineByRatio(currPt, end, 0.5);

	return `${lineTo(start)}${curveTo(bzStart, bzEnd, end)}`;
};

export const getFinishToStartPath = ({
	sourcePoint,
	targetPoint,
	isTargetMilestone
}) => {
	const sourceX = getEndX(sourcePoint);
	const targetX = getStartX(targetPoint, isTargetMilestone);
	const verticalLineStartPoint = sourceX + DEPENDENCY_LINE_OFFSET;
	const offset = targetX - sourceX - 2 * DEPENDENCY_LINE_OFFSET;

	/**
	 * Handles source behind the target
	 */
	if (offset > 0) {
		return [
			sourceX,
			verticalLineStartPoint,
			verticalLineStartPoint,
			targetX
		];
	}

	/**
     * Handles reverse case sourceIndex > targetIndex
     * Handles source after the target
     */
	const verticalLineEndPoint = targetX - DEPENDENCY_LINE_OFFSET;

	return [
		sourceX,
		verticalLineStartPoint,
		verticalLineStartPoint,
		verticalLineEndPoint,
		verticalLineEndPoint,
		targetX
	];
};

export const getStartToFinishPath = ({
	sourcePoint,
	targetPoint,
	isTargetMilestone
}) => {
	const sourceX = getStartX(sourcePoint);
	const targetX = getEndX(targetPoint, isTargetMilestone);
	const verticalLineStartPoint = sourceX - DEPENDENCY_LINE_OFFSET;
	const offset = targetX - sourceX + 2 * DEPENDENCY_LINE_OFFSET;

	if (offset < 0) {
		return [
			sourceX,
			verticalLineStartPoint,
			verticalLineStartPoint,
			targetX
		];
	}
	const verticalLineEndPoint = targetX + DEPENDENCY_LINE_OFFSET;

	return [
		sourceX,
		verticalLineStartPoint,
		verticalLineStartPoint,
		verticalLineEndPoint,
		verticalLineEndPoint,
		targetX
	];
};

export const getStartToStartPath = ({ sourcePoint, targetPoint, isTargetMilestone }) => {
	const sourceX = getStartX(sourcePoint);
	const targetX = getStartX(targetPoint, isTargetMilestone);
	const offset = targetX - sourceX;
	let verticalLineStartPoint = sourceX - DEPENDENCY_LINE_OFFSET;

	if (offset < 0) {
		verticalLineStartPoint -= Math.abs(offset);
	}

	return [
		sourceX,
		verticalLineStartPoint,
		verticalLineStartPoint,
		targetX
	];
};

export const getFinishToFinishPath = ({ sourcePoint, targetPoint, isTargetMilestone }) => {
	const sourceX = getEndX(sourcePoint);
	const targetX = getEndX(targetPoint, isTargetMilestone);
	const offset = targetX - sourceX;
	let verticalLineStartPoint = sourceX + DEPENDENCY_LINE_OFFSET;

	if (offset > 0) {
		verticalLineStartPoint += Math.abs(offset);
	}

	return [
		sourceX,
		verticalLineStartPoint,
		verticalLineStartPoint,
		targetX
	];
};

export const getLinkKey = (source, target) => {
	return `${source.id}-${target.id}`;
};

export const getPathPoints = (xCoOrdinates, midSourceY, midTargetY, rowHeight) => {
	const len = xCoOrdinates.length;
	const points = [
		[xCoOrdinates[0], midSourceY],
		[xCoOrdinates[1], midSourceY]
	];
	// is either 4 or 6
	if (len === 6) {
		const sourceOffsetY = midSourceY < midTargetY
			? midSourceY + rowHeight/2
			: midSourceY - rowHeight/2;

		points.push([xCoOrdinates[2], sourceOffsetY]);
		points.push([xCoOrdinates[3], sourceOffsetY]);
	}
	points.push([xCoOrdinates[len-2], midTargetY]);
	points.push([xCoOrdinates[len-1], midTargetY]);

	return points;
};

/**
 * This method is called at rendering time, to convert x,y coordinates to a path string
 * Since every dependency is either a 4 or 6 point line, the y coordinates of individual
 * points are adjusted based on source and target y positions
 * @param {Array} xCoOrdinates - Array of x position of points
 * @param {Number} midSourceY - Y position of midpoint of source task
 * @param {Number} midSourceY - Y position of midpoint of target task
 * @param {Number} rowHeight - rowHeight from config
 */
export const getPath = (xCoOrdinates, midSourceY, midTargetY, rowHeight, curveRadius) => {
	const points = getPathPoints(xCoOrdinates, midSourceY, midTargetY, rowHeight),
		lastIndex = points.length-1;

	let pathStr = moveTo(points[0]);
	const radius = curveRadius * rowHeight / 4;
	for (let i=1; i<lastIndex; i++) {
		pathStr += turnThrough(points[i-1], points[i], points[i+1], radius); // Radius parameter range [0, rowHeight/4]
	}
	pathStr += lineTo(points[lastIndex]);

	return pathStr;
};

/**
 * This method returns dependency positionMap which countains xCoordinates, source and target task id's
 * @param {Array} links - links prop
 * @param {Tree} tree - tree object
 * @param {Object} taskPositionMap - Task position map which contains x-coordinates of task
 */
export const getDependencyLines = ({
	links = [],
	taskPositionMap = {},
	tree
}) => {
	const linkPositionMap = {};
	const treeUtils = getTreeUtils(tree);
	if (tree) {
		links.forEach(link => {
			const { source, target } = link;
			const _sourceTask = treeUtils.getNodeById(source);
			const _targetTask = treeUtils.getNodeById(target);
			if (!_sourceTask || !_targetTask) {
				return;
			}
			const sourcePoint = taskPositionMap[_sourceTask.id];
			const targetPoint = taskPositionMap[_targetTask.id];

			if (!sourcePoint || !targetPoint) {
				return;
			}

			const _metaData = {
				sourcePoint,
				targetPoint,
				isTargetMilestone: _targetTask.taskData.type === MILESTONE
			};

			let xCoOrdinates;

			if (link.type === FINISH_TO_START) {
				xCoOrdinates = getFinishToStartPath(_metaData);
			} else if (link.type === START_TO_START) {
				xCoOrdinates = getStartToStartPath(_metaData);
			} else if (link.type === FINISH_TO_FINISH) {
				xCoOrdinates = getFinishToFinishPath(_metaData);
			} else if (link.type === START_TO_FINISH) {
				xCoOrdinates = getStartToFinishPath(_metaData);
			}

			if (!isEmpty(xCoOrdinates)) {
				linkPositionMap[link.id] = {
					xCoOrdinates,
					link
				};
			}
		});
	}

	return linkPositionMap;
};

export const getDependencyPathStrings = (linkPositionMap, tree, rowHeight, dependencyRadius = 0) => {
	const dependencyLinePaths = [];
	const treeUtils = getTreeUtils(tree);
	const linkPositionValueArray = Object.values(linkPositionMap);
	const curveRadius = Math.max(Math.min(dependencyRadius, 1), 0);

	for (let index = 0; index < linkPositionValueArray.length; index++) {
		const { xCoOrdinates, link: { source, target, id: linkId } } = linkPositionValueArray[index];
		const _sourceTask = treeUtils.getNodeById(source);
		const _targetTask = treeUtils.getNodeById(target);
		// Drawing dependencies only if both tasks are not collapsed/filtered out

		if (isVisible(_sourceTask) && isVisible(_targetTask)) {
			// Generating y positions based on sourceTask's and targetTask's index
			const midSourceY = (_sourceTask.index + 0.5) * rowHeight;
			const midTargetY = (_targetTask.index + 0.5) * rowHeight;

			// Generating the points string based on x,y positions.
			dependencyLinePaths.push({
				linkId,
				pointString: getPath(xCoOrdinates, midSourceY, midTargetY, rowHeight, curveRadius)
			});
		}
	}

	return dependencyLinePaths;
};

export const doesDependencyExist = (links, newLink) => {
	const { source: newSource, target: newTarget, type: newType } = newLink;

	return links
		.findIndex((
			{ source, target, type }
		) => target === newTarget && type === newType && source === newSource) !== -1;
};
