import isNumber from 'lodash/isNumber';
import DateHelper from './DateHelper';
import { TASK_TYPES, TIME_UNITS } from '../constants';
import { createLog } from '../../../util/logger';
const LOG = createLog('componentActions');

const { MILESTONE } = TASK_TYPES;
const { MILLISECOND } = TIME_UNITS;
const {
	getDurationInMilliseconds,
	getDurationInDays,
	getNextUnit,
	getISODateFormattedValue,
	getISODateTimeFormattedValue,
	getNativeDate,
	getNativeDiff
} = DateHelper;

export const getTaskWidth = (start, end, unit, width, step = 1) => {
	const diff = getNativeDiff(start, end, unit);

	return diff * width / step;
};

export const isVisible = (task) => {
	if (!task || typeof task !== 'object') {
		return false;
	}

	return isNumber(task.index) && task.visible;
};

export const getTaskPosition = ({
	timelineData: {
		precision,
		startDate: timelineStartDate,
		taskHeight,
		zoomLevel: {
			config: {
				bottom: {
					width,
					rangeUnit,
					step
				}
			}
		}
	},
	startDate,
	endDate,
	type
}) => {
	if (precision === TIME_UNITS.DAY) { // Days are rounded irrespective of time component
		startDate = startDate.slice(0, 10);
		endDate = getNativeDate(endDate.slice(0, 10), true);
	}

	return {
		width: type === MILESTONE ? taskHeight : getTaskWidth(startDate, endDate, rangeUnit, width, step),
		left: getTaskWidth(timelineStartDate, startDate, rangeUnit, width, step)
	};
};

/**
 * @param {Array} tasks - tasks prop
 * @param {object} timelineData
 */
export const getTaskPositionMap = ({
	tasks = [],
	timelineData
}) => {
	const positionMap = {};

	tasks.forEach(task => {
		try {
			const { id } = task;
			const startDate = task.startDate.value;
			const endDate = task.endDate.value;
			const type = task.type;


			positionMap[id] = getTaskPosition({
				timelineData,
				startDate,
				endDate,
				type
			});
		} catch (e) {
			LOG.error(`Incorrect startDate/endDate format on Task: ${task.id}`);
		}
	});

	return positionMap;
};

export const getDateFromPostion = ({
	timelineData: {
		precision,
		startDate: timelineStartDate,
		zoomLevel: {
			config: {
				bottom: {
					width,
					rangeUnit,
					step
				}
			}
		}
	},
	position
}) => {
	if (precision === TIME_UNITS.DAY) {
		return getISODateFormattedValue(getNextUnit(
			timelineStartDate,
			TIME_UNITS.DAY,
			getDurationInDays(position * step / width, rangeUnit)
		));
	} else {
		return getISODateTimeFormattedValue(getNextUnit(
			timelineStartDate,
			MILLISECOND,
			getDurationInMilliseconds(position * step / width, rangeUnit)
		));
	}
};

export const getEndDatefromStartDateOffset = (origStart, newStart, origEnd, precision) => {
	const startDiff = getNativeDiff(origStart, newStart, precision);
	const formatFn = precision === TIME_UNITS.DAY ? getISODateFormattedValue : getISODateTimeFormattedValue;

	return formatFn(getNextUnit(origEnd, precision, startDiff));
};
