import {
	renderGridHeader,
	renderGridBody,
	renderVirtualScroller,
	gridConstants,
	getScrollSynchronizer
} from '@devsnc/sn-datagrid';
import renderZoomController from './timeline/zoomController';
import './timeline/header';
import './timeline/body';
import renderResizer from './resizer';
import { SCROLLBAR_HEIGHT, TIMELINE_SCROLL_REFS, GRID, DEFAULT, TIMELINE, VIRTUAL_SCROLL, RTL, TOOLBAR_HEIGHT, VIRTUAL_SCROLLBAR_HEIGHT } from './constants';
import { createRef, Fragment } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line
import '@servicenow/now-loader';
import '@servicenow/now-toggle';
import { renderConfigPanelOptions } from './views/configPanel';
import { DEFAULT_SCALES } from './timeline/constants';
import { PERSONALIZE, HELP_LABEL } from '../constants/translated';

const { PRIMARY, SCROLL_ENDED, SCROLL_POSITION_UPDATED, SIDE_PANELS } = gridConstants;

const getScrollbarWidth = (state) => {
	return state.scrollHeight > state.visibleHeight ? state.scrollbarWidth : 0;
};


const renderHeader = ({
	dispatch,
	state,
	ganttContainerRef,
	configSectionWidth,
	allowRowsReordering,
	ganttScrollerContainer,
	setRef,
	syncScroll
}) => {
	const { layout, properties: { dir }, config, timelineData, selectedCell, holidays } = state;
	const { gridWidth } = config;

	return (
		<div
			className="sn-gantt-header-container"
			style={{
				height: `${config.headerHeight}px`,
				minHeight: `${config.headerHeight}px`,
				[state.properties.dir === RTL ? 'paddingLeft' : 'paddingRight']: `${getScrollbarWidth(state)}px`
			}}
		>
			{layout !== TIMELINE
				? <div style={{ width: `${gridWidth}%` }}>
					{renderGridHeader({
						state,
						dispatch,
						setRef,
						syncScroll,
						headerHeight: config.headerHeight,
						configSectionWidth,
						allowRowsReordering,
						bodyWrapperRef: ganttScrollerContainer
					})}
				</div>: <Fragment />}
			{layout === DEFAULT
				? renderResizer({
					dispatch,
					dir,
					gridWidth: config.gridWidth,
					ganttContainerRef,
					height: config.headerHeight
				}): <Fragment />
			}
			{layout !== GRID
				? <sn-gantt-timeline-header
					selectedCell={selectedCell}
					bodyWrapperRef={ganttScrollerContainer}
					config={config}
					timelineData={timelineData}
					possibleZoomLevels={state.possibleZoomLevels}
					setRef={setRef}
					syncScroll={syncScroll}
					holidays={holidays}
				/>: <Fragment />}
		</div>
	);
};

const renderVirtualScrollbars = ({
	state,
	configSectionWidth,
	setRef,
	syncScroll
}) => {
	const virtualTimelineScroll = createRef();
	const {
		config: { gridWidth },
		layout,
		timelineData: {
			totalWidth
		}
	} = state;

	setRef('virtualTimelineScroll', virtualTimelineScroll, TIMELINE_SCROLL_REFS);

	return (
		<div className="virtual-scrollbars-container"
			style={{
				[state.properties.dir === RTL ? 'paddingLeft' : 'paddingRight']: `${getScrollbarWidth(state)}px`,
				height: `${VIRTUAL_SCROLLBAR_HEIGHT}px`
			}}>
			{layout !== TIMELINE
				? <div style={{ width: `${gridWidth}%` }}>
					{renderVirtualScroller({ state, setRef, syncScroll, configSectionWidth })}
				</div>
				: <Fragment />
			}
			{layout !== GRID
				? <div
					className="timeline-scroll"
					ref={virtualTimelineScroll}
					on-scroll={e =>
						syncScroll(e, TIMELINE_SCROLL_REFS, VIRTUAL_SCROLL)
					}>
					<div
						className="inner-scroller"
						style={{
							width: `${totalWidth}px`
						}}
					></div>
				</div>
				: <Fragment />
			}
		</div>
	);
};

const renderSection = ({
	dispatch,
	state,
	visibleTasks,
	ganttContainerRef,
	baselineDisplayStatus,
	configSectionWidth,
	allowRowsReordering,
	ganttScrollerContainer,
	setRef,
	syncScroll
}) => {
	const {
		layout,
		scrollHeight,
		visibleHeight,
		tree,
		selectedCell,
		isScrolling,
		positionMaps,
		properties: { links },
		config,
		timelineData,
		bottomScaleData
	} = state;
	const { dir } = state.properties;
	const { gridWidth, smartRendering } = config;
	const bodyHeight = `${
		scrollHeight < visibleHeight
			?	visibleHeight
			:	scrollHeight + SCROLLBAR_HEIGHT }px`;

	return (
		<section
			className="sn-gantt-body-container">
			<div
				className="sn-gantt-inner-scroller"
				ref={ganttScrollerContainer}
				on-scroll={e => {
					if (smartRendering) {
						dispatch(SCROLL_POSITION_UPDATED, { scrollTop: e.target.scrollTop });
						dispatch(SCROLL_ENDED, { scrollTop: e.target.scrollTop });
					}
				}}
			>
				{
					!state.isLoading ? <Fragment>
						{layout !== TIMELINE
							?<div style={{ width: `${gridWidth}%` }}>
								{renderGridBody({
									visibleTasks,
									state,
									dispatch,
									bodyWrapperRef: ganttScrollerContainer,
									setRef,
									syncScroll,
									height: bodyHeight,
									segment: PRIMARY,
									configSectionWidth,
									allowRowsReordering
								})}
							</div>: <Fragment />}
						{layout === DEFAULT
							? renderResizer({
								dispatch,
								dir,
								gridWidth,
								ganttContainerRef,
								height: Math.max(scrollHeight + SCROLLBAR_HEIGHT, visibleHeight)
							}): <Fragment />
						}
						{layout !== GRID ? <sn-gantt-timeline-body
							style={{ height: bodyHeight }}
							positionMaps={positionMaps}
							config={config}
							timelineData={timelineData}
							columns={state.properties.columns}
							baselineStatus={baselineDisplayStatus}
							syncScroll={syncScroll}
							setRef={setRef}
							tree={tree}
							visibleTasks={visibleTasks}
							links={links}
							selectedCell={selectedCell}
							bodyWrapperRef={ganttScrollerContainer}
							isScrollingVertically={isScrolling}
							bottomScaleData={bottomScaleData}
						>
						</sn-gantt-timeline-body>: <Fragment />}
					</Fragment> : <Fragment />
				}
				{
					state.isLoading ? <now-loader className="loader" size="lg" /> : <Fragment />
				}
			</div>
			{timelineData ? renderVirtualScrollbars({
				state,
				configSectionWidth,
				setRef,
				syncScroll
			}) : <Fragment />}
			{
				layout !== GRID	&&
				timelineData &&
				timelineData.zoomLevel.id !== DEFAULT_SCALES.AUTOFIT
					? renderZoomController({ zoomPercentage: timelineData.zoomPercentage })
					: <Fragment />
			}
		</section>
	);
};

const view = (state, { dispatch }) => {
	const { setRef, syncScroll } = getScrollSynchronizer();
	const {
		config,
		sidePanel,
		baselineDisplayStatus,
		allowRowsReordering,
		configSectionWidth,
		visibleTasks
	} = state;

	const {
		links,
		tasks: propTasks,
		enableBaselines
	} = state.properties;
	const {
		toolbar,
		rowGroup,
		filter
	} = config;

	const ganttContainerRef = createRef();
	const ganttScrollerContainer = createRef();

	setRef('ganttContainerRef', ganttContainerRef);
	setRef('ganttScrollerContainerRef', ganttScrollerContainer);

	return (
		<div
			className="now-gantt-container"
			style={{ visibility: `${state.visibleHeight === 0 ? 'hidden': 'visible'}` }}
			role="main"
		>
			<div className="scrollbar-measure"></div>
			{
				toolbar.display
					? <sn-datagrid-header-toolbar {...{
						...toolbar,
						count: propTasks.length,
						rowGroup,
						filter,
						style: { height: `${TOOLBAR_HEIGHT}px` } // For calculating correct visible Height
					}}>
						<slot slot='toolbar-icons' name="toolbar-icons"/>
					</sn-datagrid-header-toolbar>
					: <Fragment />
			}
			<div className="sn-gantt"
				ref={ganttContainerRef}
				role="grid"
			>
				{renderHeader({
					ganttContainerRef,
					dispatch,
					state,
					configSectionWidth,
					allowRowsReordering,
					ganttScrollerContainer,
					setRef,
					syncScroll
				})}
				{renderSection({
					dispatch,
					state,
					visibleTasks,
					links,
					ganttContainerRef,
					baselineDisplayStatus,
					configSectionWidth,
					allowRowsReordering,
					ganttScrollerContainer,
					setRef,
					syncScroll
				})}
				{
					sidePanel.display
						? <sn-datagrid-config-panel className="side-panel" panelTitle={
							sidePanel.selectedPanel === SIDE_PANELS.SETTINGS_PANEL_TOGGLE
								? PERSONALIZE
								: HELP_LABEL
						}>
							<div slot="sn-datagrid-panel-content" className="side-panel-container">
								{
									sidePanel.selectedPanel === SIDE_PANELS.SETTINGS_PANEL_TOGGLE
										? renderConfigPanelOptions({
											state,
											dispatch,
											ganttContainerRef,
											enableBaselines
										})
										: <sn-datagrid-help-panel
											sidePanel={state.sidePanel}
											readonly={state.config.readonly}
											rowCopyPaste={state.config.rowCopyPaste}
											reorderRows={state.config.reorderRows}
											previewRecordIcon={state.config.previewRecordIcon}
											setRowContextMenu={state.config.setRowContextMenu}
											reorderColumns={state.config.reorderColumns}
										></sn-datagrid-help-panel>
								}
							</div>
						</sn-datagrid-config-panel>
						: <Fragment />
				}
			</div>
		</div>
	);
};

export default view;
