import DateHelper from './DateHelper';
import isFunction from 'lodash/isFunction';
import { TIME_UNITS, TWO_CHAR_WIDTH, FOUR_CHAR_WIDTH } from '../constants';
import Timeline from '../models/Timeline';
import { getTaskAndLinkPositions } from './TaskAndLinkPositionManager';

const {
	getPrevUnit,
	getNextUnit,
	getDate,
	getEndOfUnit,
	getFormattedValue,
	getFormattedValueWithHoliday,
	getISODateTimeFormattedValue,
	getStartOfUnit,
	getNativeDiff
} = DateHelper;

export const getBottomScaleUnitCount = ({
	startDate,
	endDate,
	zoomLevel
}) => {
	const { step, rangeUnit } = zoomLevel.config.bottom;
	const extraUnits = 2 * zoomLevel.extraUnits / step;
	const actualUnits = Math.max(
		Math.ceil(
			getNativeDiff(
				startDate,
				endDate,
				rangeUnit
			) / step
		), 1);

	return actualUnits + extraUnits;
};

/**
 * @description This method returns a batch of scale units based on startDate, count, and the scale config
 */
export const getScaleUnits = (start, count, scaleConfig, config, holidays) => {
	const { rangeUnit, format, step } = scaleConfig;
	const result = [];
	let current = getDate(start);
	for (let i = 0; i < count; i++) {
		result.push(getFormattedValueWithHoliday(current, format, config, holidays));
		current = getNextUnit(
			getStartOfUnit(current, rangeUnit),
			rangeUnit,
			step
		);
	}

	return result;
};

export const getTopScaleForBottomScale = (startPositionDate, timelineEndDate, config, bottomCount) => {
	const { rangeUnit, format } = config.top;
	const topScales = [],
		_timelineEndDate = getDate(timelineEndDate),
		formatFn = isFunction(format)
			? format
			: getFormattedValue;
	let current = getDate(startPositionDate),
		currentEnd,
		count = 0;

	while (count < bottomCount && currentEnd !== _timelineEndDate) {
		currentEnd = getEndOfUnit(
			current,
			rangeUnit
		);
		currentEnd = currentEnd > _timelineEndDate ? _timelineEndDate : currentEnd;
		const bottomUnitCount = getBottomScaleUnitCount({
			startDate: current,
			endDate: currentEnd,
			zoomLevel: { config, extraUnits: 0 }
		});

		topScales.push({
			text: formatFn(current, format),
			width: bottomUnitCount * config.bottom.width
		});
		count += bottomUnitCount;
		current = getNextUnit(
			currentEnd,
			TIME_UNITS.SECOND
		);
	}

	return topScales;
};

export const calcStartDate = ({
	startDate,
	rangeUnit,
	extraUnits
}) => {
	const prevUnitDate = getPrevUnit(
		startDate,
		rangeUnit,
		extraUnits
	);

	return getISODateTimeFormattedValue(getStartOfUnit(prevUnitDate, rangeUnit));
};

export const calcEndDate = ({
	endDate,
	rangeUnit,
	extraUnits
}) => {
	const nextUnitDate = getNextUnit(
		endDate,
		rangeUnit,
		extraUnits
	);

	return getISODateTimeFormattedValue(getEndOfUnit(nextUnitDate, rangeUnit));
};

export const getTimelineData = ({
	config: {
		zoomLevel,
		taskHeight,
		precision
	},
	visibleWidth,
	multiplicationFactor,
	possibleZoomLevels,
	startDate,
	endDate
}) => {
	const timelineData = new Timeline({
		startDate,
		endDate,
		zoomLevel,
		visibleWidth,
		taskHeight,
		multiplicationFactor,
		possibleZoomLevels,
		precision
	});

	return timelineData;
};

export const getTimelineDataWithPositions = ({
	startDate,
	endDate,
	config,
	visibleWidth,
	multiplicationFactor,
	possibleZoomLevels,
	tasks,
	links,
	tree
}) => {
	const timelineData = getTimelineData({
		startDate,
		endDate,
		config,
		visibleWidth,
		multiplicationFactor,
		possibleZoomLevels
	});

	const positionMaps = getTaskAndLinkPositions({
		tasks,
		links,
		config,
		timelineData,
		tree
	});

	return {
		timelineData,
		positionMaps
	};
};

/**
 * @description This method return the bottom unit width which fits all units in the visibleWidth
 */
export const getScaleWidth = ({
	startDate,
	endDate,
	zoomLevel,
	visibleWidth
}) => {
	const bottomScaleUnits = getBottomScaleUnitCount({
		startDate,
		endDate,
		zoomLevel
	});

	return Math.ceil(visibleWidth / bottomScaleUnits);
};


/**
 * @description This method increases end date if current end date is less and leaves whitespace
 */
export const getAdjustedEndDate = ({
	startDate,
	endDate,
	zoomLevel,
	visibleWidth
}) => {
	let updatedEndDate = endDate;
	const { width: unitWidth, rangeUnit, step } = zoomLevel.config.bottom;
	const bottomScaleUnits = getBottomScaleUnitCount({
		startDate,
		endDate,
		zoomLevel
	});
	const totalWidth = unitWidth * bottomScaleUnits;

	if (totalWidth < visibleWidth) {
		const additionalUnits = Math.ceil(Math.abs(totalWidth - visibleWidth) / unitWidth) * step;

		updatedEndDate = getISODateTimeFormattedValue(
			getNextUnit(
				endDate,
				rangeUnit,
				additionalUnits
			)
		);
	}

	return updatedEndDate;
};

/**
 * @description Method returns a format which fits in the given width
 */
export const getWidthAdjustedFormat = ({ rangeUnit, width, format }) => {
	switch (rangeUnit) {
		case TIME_UNITS.MONTH:
			if (width <= TWO_CHAR_WIDTH) {
				return 'MM';
			} else if (width <= FOUR_CHAR_WIDTH) {
				return 'MMM';
			}
			break;
		case TIME_UNITS.WEEK:
			if (width < TWO_CHAR_WIDTH) {
				return 'W';
			}
			break;
		case TIME_UNITS.DAY:
			if (width < TWO_CHAR_WIDTH) {
				return 'DD';
			}
			break;
		default:
			return format;
	}

	return format;
};
