import { NEW_TASK_ID } from '../constants';

// New task is added at the bottom of the grid
export const scrollNewTaskIntoView = (host, tasks = []) => {
	if (tasks.length && tasks[tasks.length - 1].id === NEW_TASK_ID) {
		const ganttWrapper = host.shadowRoot.querySelector('.sn-gantt-inner-scroller');

		setTimeout(() => {
			ganttWrapper.scrollBy({
				top: ganttWrapper.scrollHeight,
				left: 0,
				behavior: 'smooth'
			});
		}, 20);
	}
};
