import moment from 'moment-timezone';
import isString from 'lodash/isString';
import isFunction from 'lodash/isFunction';
import {
	TIME_UNITS,
	DEFAULT_DATE_FORMAT,
	DEFAULT_DATE_TIME_FORMAT,
	DEFAULT_SCALES,
	TIME_UNIT_FORMAT
} from '../constants';
import { createLog } from '../../../util/logger';
const LOG = createLog('componentActions');

const {
	SECOND,
	MINUTE,
	HOUR,
	MONTH,
	QUARTER
} = TIME_UNITS;

let defaultFormat;
let defaultDateFormat;
let currentTimeZone;

const getMonthDifference = (date2, date1) => {
	let start = date1;
	let end = date2;
	let sign = 1;
	if (date1 > date2) {
		start = date2;
		end = date1;
		sign = -1;
	}

	const yearsDifference = end.getFullYear() - start.getFullYear();
	let monthsDifference = end.getMonth() - start.getMonth();
	const daysDifference = end.getDate() - start.getDate();

	if (daysDifference !== 0) {
		monthsDifference += daysDifference / 30;
	}
	const absoluteDiff = yearsDifference * 12 + monthsDifference;

	return sign * absoluteDiff;
};

export const configureMoment = ({
	dateFormat = DEFAULT_DATE_FORMAT,
	dateTimeFormat = DEFAULT_DATE_TIME_FORMAT,
	timeZone = null,
	weekStartsOn = 0
}) => {
	moment.updateLocale('en', {
		week: {
			dow: weekStartsOn
		}
	});
	defaultFormat = dateTimeFormat;
	defaultDateFormat = dateFormat;
	currentTimeZone = timeZone;
};

const getDate = (date) => {
	try {
		if (!date) {
			return currentTimeZone
				? moment().clone().tz(currentTimeZone)
				: moment();
		} else if (isString(date) || date instanceof Date) { // Expecting all dates to be in ISO format
			return moment(date);
		} else if (date instanceof moment) {
			return date.clone();
		} else {
			return date;
		}
	} catch (e) {
		LOG.error(e);

		return moment();
	}
};

/**
 * This method handles the conversion of Date Strings to JS date without appending timezone time
 * @param {String} date 
 */
const getNativeDate = (date, isEndOfDay) => {
	if (typeof date === 'string') {
		if (date.length === 10) {
			/**
			 * Transforming YYYY-MM-DD to YYYY-MM-DDTHH:MM:SS
			 */
			date += isEndOfDay
				? 'T23:59:59'
				: 'T00:00:00';
		} else if (date.length === 19) {
			/**
			 * Transforming YYYY-MM-DD HH:MM:SS to YYYY-MM-DDTHH:MM:SS
			 */
			date.replace(' ', 'T');
		}
	}

	return date ? new Date(date): new Date();
};

const getToday = () => {
	return getDate();
};

const getFormattedValue = (date, format) => {
	return getDate(date).format(format);
};

const getFormattedValueWithHoliday = (date, format, config, holidays) => {
	const { weekends, zoomLevel } = config;
	const formatFn = isFunction(format)
		? format
		: getFormattedValue;
	const text = formatFn(date, format);
	let holiday = false;
	const displayHolidays = zoomLevel === DEFAULT_SCALES.WEEK ||
		zoomLevel === DEFAULT_SCALES.DAY ||
		zoomLevel === DEFAULT_SCALES.AUTOFIT && format === TIME_UNIT_FORMAT[TIME_UNITS.DAY];
	// It's not possible to have a week format in autofit view
	if (displayHolidays) {
		holiday = holiday || weekends.includes(date.day());
		holiday = holiday || holidays.some((hol) => date.isSame(hol));
	}

	return {
		text,
		holiday
	};
};

// Unused now, can maybe be used for sending display value after resize
const getDateFormattedValue= (date) => {
	return getDate(date).format(defaultDateFormat);
};
// Unused now, can maybe be used for sending display value after resize
const getDateTimeFormattedValue = (date) => {
	return getDate(date).format(defaultFormat);
};

const getISODateTimeFormattedValue = (date) => {
	return getDate(date).format().slice(0, 19);
};
const getISODateFormattedValue = (date) => {
	return getDate(date).format().slice(0, 10);
};

// Moment diff supports these measurement units: years, months, weeks, days, hours, minutes, and seconds
const getDiff = (start, end, unit) => {
	const _start = getDate(start);
	const _end = getDate(end);

	return _end.diff(_start, unit, true);
};
const getNativeDiff = (start, end, unit) => {
	let diff;
	const startDate = getNativeDate(start);
	const endDate = getNativeDate(end);

	switch (unit) {
		case 'year': diff = getMonthDifference(endDate, startDate) / 12; break;
		case 'month': diff = getMonthDifference(endDate, startDate); break;
		case 'quarter': diff = getMonthDifference(endDate, startDate) / 3; break;
		case 'second': diff = (endDate - startDate) / 1e3; break; // 1000
		case 'minute': diff = (endDate - startDate) / 6e4; break; // 1000 * 60
		case 'hour': diff = (endDate - startDate) / 36e5; break; // 1000 * 60 * 60
		case 'day': diff = (endDate - startDate) / 864e5; break; // 1000 * 60 * 60 * 24
		case 'week': diff = (endDate - startDate) / 6048e5; break; // 1000 * 60 * 60 * 24 * 7
		default: diff = endDate - startDate;
	}

	return diff;
};

const getDiffOfUnit = (start, end, unit) => {
	return unit === QUARTER
		? getDiff(start, end, MONTH) / 3
		: getDiff(start, end, unit);
};

const getStartOfUnit = (date, unit) => {
	return getDate(date).startOf(unit);
};

const getEndOfUnit = (date, unit) => {
	return getDate(date).endOf(unit);
};

const getPrevUnit = (date, unit, count = 1) => {
	return getDate(date).subtract(count, unit);
};

const getNextUnit = (date, unit, count = 1) => {
	return getDate(date).add(count, unit);
};

const isAfter = (start, end) => {
	return getDate(start).isAfter(end);
};

const isTimeUnit = (unit) => {
	return [SECOND, MINUTE, HOUR].indexOf(unit) > -1;
};

const getDurationInMilliseconds = (count = 1, unit) => {
	return moment.duration(count, unit).asMilliseconds();
};
const getDurationInDays = (count = 1, unit) => {
	return moment.duration(count, unit).asDays();
};

export default {
	isAfter,
	getNextUnit,
	getPrevUnit,
	getEndOfUnit,
	getStartOfUnit,
	getDiffOfUnit,
	getNativeDiff,
	getFormattedValue,
	getFormattedValueWithHoliday,
	getDateTimeFormattedValue,
	getDateFormattedValue,
	getISODateTimeFormattedValue,
	getISODateFormattedValue,
	getDate,
	getNativeDate,
	getToday,
	isTimeUnit,
	getDurationInMilliseconds,
	getDurationInDays
};
