import { resizeGrid } from '../helpers/resizer';
import { gridConstants } from '@devsnc/sn-datagrid';
import { createRef } from '@servicenow/ui-renderer-snabbdom';

const renderResizer = ({
	dispatch,
	dir,
	gridWidth,
	ganttContainerRef,
	height
}) => {
	const resizerRef = createRef();
	const style = {
		height: `${height}px`
	};

	const { CONTAINER_SELECTED } = gridConstants;

	const triggerResizeEvent = () => {
		resizeGrid(
			dispatch,
			dir,
			gridWidth,
			ganttContainerRef
		);
	};

	return (
		<div
			on-mousedown={() => {
				dispatch(CONTAINER_SELECTED);
				triggerResizeEvent(resizerRef.current);
			}}
			style={style}
			ref={resizerRef}
			className="resizer"
		>
			<div className="resizer-line" >
				<div className="resizer-icon">
				</div>
			</div>
		</div>
	);
};

export default renderResizer;
