import renderProgress from '../timeline/body/renderers/progress';
import isEmpty from 'lodash/isEmpty';
import get from 'lodash/get';
import memoize from 'lodash/memoize';
import { PARENT_TASK_UMBRELLA_MINIMUM_WIDITH } from '../timeline/constants';
import { Fragment } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line

export const getBarColors = memoize(({
	barStyles,
	$isParent
}) => {
	const barColor = {};
	const percentColors = {};

	if ($isParent) {
		const color = get(barStyles, 'summary.color');
		const percentColor = get(barStyles, 'summary.percentColor');

		if (!isEmpty(color)) {
			barColor.color= color;
			barColor.backgroundColor= color;
		}

		if (!isEmpty(percentColor)) {
			percentColors.backgroundColor = percentColor;
		}
	} else {
		const color = get(barStyles, 'leaf.color');
		const percentColor = get(barStyles, 'leaf.percentColor');

		if (!isEmpty(color)) {
			barColor.color= color;
			barColor.backgroundColor= color;
		}

		if (!isEmpty(percentColor)) {
			percentColors.backgroundColor = percentColor;
		}
	}

	return [barColor, percentColors];
},
(...args) => JSON.stringify(args)
);

export default {
	barTemplate: ({
		$node,
		$isParent,
		$config,
		$rect: { width }
	}) => {
		const { barStyles } = $config;
		const [barColor, percentColors] = getBarColors({
			barStyles,
			$isParent
		});
		const classList = `sn-gantt-task ${$isParent
			? 'sn-gantt-task-parent'
			: 'sn-gantt-task-leaf'
		}`;

		return (
			<div className={classList}
				style={barColor}
				tabIndex="-1"
			>
				{renderProgress({
					task: $node,
					percentColors,
					isParent: $isParent
				})}
				{
					width >= PARENT_TASK_UMBRELLA_MINIMUM_WIDITH && $isParent ? <div className="umbrella"></div> : <Fragment />
				}
			</div>
		);
	},
	progressTemplate: () => {}
};
