import Scale from './Scale';
import BottomScale from './BottomScale';

export default class ZoomLevel {
	constructor(
		{
			id,
			label = '',
			config,
			extraUnits = 1
		}
	) {
		if (!id) {
			throw new Error('Zoom level id should be provided');
		}

		if (!config || Object.keys(config).length === 0 || !config.top || !config.bottom) {
			throw new Error('Config should not be empty and provide top and bottom objects');
		}

		/**
		 * extra units should always be a multiple of step
		 * otherwise bottom scale will be incorrect
		 */
		const { step } = config.bottom;
		const roundedExtraUnits = Math.ceil(extraUnits / step) * step;

		this.id = id;
		this.label = label;
		this.extraUnits = roundedExtraUnits;
		this.config = {
			top: new Scale(config.top),
			bottom: new BottomScale(config.bottom, config.top)
		};
	}
}
