import DateHelper from '../helpers/DateHelper';
import { DEFAULT_ZOOM_LEVELS, ZOOM_IN_ZOOM_OUTS } from '../default/zoomLevels';

const { getDate } = DateHelper;

function calculateQuarters(startDate, endDate) {
	const startDateCurrentQuarter = startDate.quarter();
	const endDateCurrentQuarter = endDate.quarter();
	const yearGapExcludeStartAndEnd = endDate.year() - startDate.year() - 1;

	return yearGapExcludeStartAndEnd*4 + (4 - startDateCurrentQuarter) + endDateCurrentQuarter;
}

export const calculateDurations = ({
	startDate: sDate,
	endDate: eDate
}) => {
	const startDate = getDate(sDate);
	const endDate = getDate(eDate);

	const duration = {};

	DEFAULT_ZOOM_LEVELS.forEach(level => {
		const unit = level.config.bottom.rangeUnit + 's';

		switch (unit) {
			case 'quarters':
				duration[unit] = calculateQuarters(startDate, endDate);
				break;
			default:
				duration[unit] = endDate.diff(startDate, unit);
		}
	});

	return duration;
};

// TODO: Unit Test Cases
export const calculateAutoFitScale = ({ startDate, endDate, visibleWidth }) => {
	const durations = calculateDurations({
		startDate,
		endDate
	});
	const reversedZoomInZoomOuts = [...ZOOM_IN_ZOOM_OUTS].reverse();
	const zoomLength = DEFAULT_ZOOM_LEVELS.length - 1; // Exclude autofit
	const factorLength = reversedZoomInZoomOuts.length;
	const result = {
		zoomLevel: null,
		zoomPercentage: null
	};

	let diff = Infinity;

	for (let zoomIndex = 0; zoomIndex < zoomLength; zoomIndex++) {
		const {
			config: {
				bottom: {
					rangeUnit,
					width
				}
			},
			id
		} = DEFAULT_ZOOM_LEVELS[zoomIndex];
		const durationVal = durations[rangeUnit + 's'];

		for (let factorIndex = 0; factorIndex < factorLength; factorIndex++) {
			const zoomPercentage = reversedZoomInZoomOuts[factorIndex];
			const widthInCurrentUnit = (durationVal + 2) * width * zoomPercentage.multiplicationFactor;
			const currentDiff = visibleWidth - widthInCurrentUnit;

			if (currentDiff > 0) {
				if (diff > 0 && currentDiff < diff || diff < 0) {
					diff = currentDiff;

					result.zoomLevel = id;
					result.zoomPercentage = zoomPercentage;

					return result;
				}
			} else if (currentDiff < Math.abs(diff)) {
				diff = currentDiff;

				result.zoomLevel = id;
				result.zoomPercentage = zoomPercentage;
			}
		}
	}

	return result;
};
