import {
	COLUMNS_UPDATED,
	GANTT_COLUMNS_UPDATED,
	SORT_CHANGED,
	GANTT_SORT_CHANGED,
	ROW_EDIT_STARTED,
	GANTT_ROW_EDIT_STARTED,
	PREVIEW_RECORD_ICON_CLICKED,
	GANTT_PREVIEW_RECORD_ICON_CLICKED,
	FILTER_CHANGED,
	GANTT_FILTER_CHANGED,
	ROW_SELECTED_SET,
	GANTT_ROW_SELECTED_SET,
	ALL_ROWS_SELECTED,
	GANTT_ALL_ROW_SELECTED,
	ALL_ROWS_UNSELECTED,
	GANTT_ALL_ROW_UNSELECTED,
	ROW_EXPAND_SET,
	GANTT_ROW_EXPAND_SET,
	ALL_ROWS_EXPANDED,
	GANTT_ALL_ROWS_EXPANDED,
	GANTT_ALL_ROWS_COLLAPSED,
	ALL_ROWS_COLLAPSED,
	COLUMNS_RESET,
	GANTT_COLUMNS_RESET,
	TASKS_PASTED,
	GANTT_TASKS_PASTED,
	ROW_ORDER_CHANGED,
	GANTT_ROW_ORDER_CHANGED,
	INSERT_COPIED_ROWS,
	GANTT_INSERT_COPIED_ROWS
} from '../constants';
import {
	getWhiteSpaceAdjustedColumns,
	getCurrentGridWidth,
	gridConstants
} from '@devsnc/sn-datagrid';
import { getScrollBarWidthForGrid } from './componentActions';

const { COMPONENT_HOST_RESIZED } = gridConstants;

export const ganttGridActionHandlers = {
	[COLUMNS_UPDATED]: {
		stopPropagation: true,
		effect({
			action: {
				payload
			},
			dispatch,
			state,
			updateState
		}) {
			const { gridWidth, columns, config, scrollbarWidth } = state;
			if (gridWidth) {
				updateState({
					columns: getWhiteSpaceAdjustedColumns({
						gridWidth: getCurrentGridWidth({
							...state,
							scrollbarWidth: getScrollBarWidthForGrid(scrollbarWidth, config.gridWidth)
						}),
						columns
					})
				});
			}
			dispatch(GANTT_COLUMNS_UPDATED, { ...payload });
		}
	},
	[SORT_CHANGED]: {
		stopPropagation: true,
		effect({
			action: {
				payload
			},
			dispatch
		}) {
			dispatch(GANTT_SORT_CHANGED, { ...payload });
		}
	},
	[ROW_EDIT_STARTED]: {
		stopPropagation: true,
		effect({
			action: {
				payload
			},
			dispatch
		}) {
			dispatch(GANTT_ROW_EDIT_STARTED, { ...payload });
		}
	},
	[PREVIEW_RECORD_ICON_CLICKED]: {
		stopPropagation: true,
		effect({
			action: {
				payload
			},
			dispatch
		}) {
			dispatch(GANTT_PREVIEW_RECORD_ICON_CLICKED, { ...payload });
		}
	},
	[FILTER_CHANGED]: {
		stopPropagation: true,
		effect({ action: { payload }, dispatch }) {
			dispatch(GANTT_FILTER_CHANGED, { ...payload });
		}
	},
	[ROW_SELECTED_SET]: {
		stopPropagation: true,
		effect({ action: { payload }, dispatch }) {
			dispatch(GANTT_ROW_SELECTED_SET, { ...payload });
		}
	},
	[ALL_ROWS_SELECTED]: {
		stopPropagation: true,
		effect({ dispatch }) {
			dispatch(GANTT_ALL_ROW_SELECTED);
		}
	},
	[ALL_ROWS_UNSELECTED]: {
		stopPropagation: true,
		effect({ dispatch }) {
			dispatch(GANTT_ALL_ROW_UNSELECTED);
		}
	},
	[ROW_EXPAND_SET]: {
		stopPropagation: true,
		effect({ action: { payload }, dispatch }) {
			dispatch(GANTT_ROW_EXPAND_SET, { ...payload });
		}
	},
	[ALL_ROWS_EXPANDED]: {
		stopPropagation: true,
		effect({ dispatch }) {
			dispatch(COMPONENT_HOST_RESIZED);
			dispatch(GANTT_ALL_ROWS_EXPANDED);
		}
	},
	[ALL_ROWS_COLLAPSED]: {
		stopPropagation: true,
		effect({ dispatch }) {
			dispatch(COMPONENT_HOST_RESIZED,);
			dispatch(GANTT_ALL_ROWS_COLLAPSED);
		}
	},
	[COLUMNS_RESET]: {
		stopPropagation: true,
		// TODO: remove the payload from here as 'COLUMNS_RESET' doesn't have a payload
		effect({ dispatch, action: { payload } }) {
			// TODO: remove payload
			dispatch(GANTT_COLUMNS_RESET, { ...payload });
		}
	},
	[TASKS_PASTED]: {
		stopPropagation: true,
		effect({ action: { payload }, dispatch }) {
			dispatch(GANTT_TASKS_PASTED, { ...payload });
		}
	},
	[ROW_ORDER_CHANGED]: {
		stopPropagation: true,
		effect({ action: { payload }, dispatch }) {
			dispatch(GANTT_ROW_ORDER_CHANGED, { ...payload });
		}
	},
	[INSERT_COPIED_ROWS]: {
		stopPropagation: true,
		effect({ action: { payload }, dispatch }) {
			dispatch(GANTT_INSERT_COPIED_ROWS, { ...payload });
		}
	}
};
