import throttle from 'lodash/throttle';
import {
	THROTTLE_TIME,
	LINKS,
	LINK_DRAG_ENDED,
	LINK_DRAG_MOVED,
	LINK_DRAG_STARTED,
	START,
	FINISH
} from '../constants';
import {
	querySelector,
	getShadowRootHost
} from '@devsnc/sn-datagrid';
import {
	canScrollEnd,
	setScrollEnd,
	canScrollStart,
	setScrollStart,
	canScrollTop,
	setScrollTop,
	canScrollBottom,
	setScrollBottom
} from './TaskMoveManager';

let dragOk = false;
let source;
let scrollContainer;
let scrollContainerRect;
let leftScrollInterval;
let rightScrollInterval;
let topScrollIntervall;
let bottomScrollInterval;
let sourceTerminal;
let handleMouseMove;
let handleMouseUp;

const getSvgPoint = (element, x, y) => {
	const pt = element.createSVGPoint();

	pt.x = x;
	pt.y = y;

	return pt.matrixTransform(element.getScreenCTM().inverse());
};

const getType = (targetTerminal) => {
	return LINKS[sourceTerminal + '_TO_' + targetTerminal];
};

const getTerminalName = (element) => {
	if (element.classList.contains('terminal-left')) {
		return START;
	} else if (element.classList.contains('terminal-right')) {
		return FINISH;
	} else {
		return '';
	}
};

const isLinkTerminal = (element) => {
	return element.classList.contains('sn-gantt-task-link-terminal');
};

const throttledMouseMove = throttle(({
	event,
	dispatch,
	svg,
	isRTL
}) => {
	if (!scrollContainer || !scrollContainerRect) {
		return;
	}

	let point = getSvgPoint(
		svg,
		event.pageX,
		event.pageY
	);
	const metadata = {
		event,
		scrollContainer,
		scrollContainerRect,
		isRTL
	};

	clearInterval(leftScrollInterval);
	clearInterval(rightScrollInterval);
	clearInterval(topScrollIntervall);
	clearInterval(bottomScrollInterval);

	if (canScrollStart(metadata)) {
		leftScrollInterval = setInterval(() => {
			point = getSvgPoint(
				svg,
				event.pageX,
				event.pageY
			);

			setScrollStart(metadata);

			dispatch(LINK_DRAG_MOVED, {
				x2: point.x,
				y2: point.y
			});
		}, THROTTLE_TIME);
	}

	if (canScrollEnd(metadata)) {
		rightScrollInterval = setInterval(() => {
			point = getSvgPoint(
				svg,
				event.pageX,
				event.pageY
			);
			setScrollEnd(metadata);

			dispatch(LINK_DRAG_MOVED, {
				x2: point.x,
				y2: point.y
			});
		}, THROTTLE_TIME);
	}

	const verticalScrollMetadata = {
		event,
		scrollContainer,
		scrollContainerRect: scrollContainer.getBoundingClientRect()
	};

	if (canScrollTop(verticalScrollMetadata)) {
		topScrollIntervall = setInterval(() => {
			point = getSvgPoint(
				svg,
				event.pageX,
				event.pageY
			);

			const diffY = setScrollTop(verticalScrollMetadata);

			dispatch(LINK_DRAG_MOVED, {
				x2: point.x,
				y2: point.y - diffY
			});
		}, THROTTLE_TIME);
	}

	if (canScrollBottom(verticalScrollMetadata)) {
		bottomScrollInterval = setInterval(() => {
			const diffY = setScrollBottom(verticalScrollMetadata);

			point = getSvgPoint(
				svg,
				event.pageX,
				event.pageY
			);

			dispatch(LINK_DRAG_MOVED, {
				x2: point.x,
				y2: point.y + diffY
			});
		}, THROTTLE_TIME);
	}

	dispatch(LINK_DRAG_MOVED, {
		x2: point.x,
		y2: point.y
	});
}, THROTTLE_TIME);

export const onLinkDragMouseUp = ({
	event,
	dispatch,
	id
}) => {
	let type;
	let target;

	// If the mouse up location contains terminal classes then not execute window mouseup event
	if (dragOk && isLinkTerminal(event.currentTarget) && source !== id) {
		event.stopImmediatePropagation();
		type = getType(getTerminalName(event.currentTarget));
		target = id;
	}

	dispatch(LINK_DRAG_ENDED, {
		source,
		target,
		type
	});

	dragOk = false;
	source = null;
	sourceTerminal = null;
	scrollContainer = null;
	scrollContainerRect = null;
	window.removeEventListener('mousemove', handleMouseMove);
	window.removeEventListener('mouseup', handleMouseUp);
	handleMouseMove = null;
	handleMouseUp = null;
	clearInterval(leftScrollInterval);
	clearInterval(rightScrollInterval);
	clearInterval(topScrollIntervall);
	clearInterval(bottomScrollInterval);
};

const onLinkDragStart = ({
	event,
	dispatch,
	taskRect,
	yPos,
	id,
	awayFromTask,
	isRTL
}) => {
	event.stopImmediatePropagation();

	if (isLinkTerminal(event.currentTarget)) {
		const svg = querySelector(event.target, '.drawingLinkSvg');

		// scrollContainer=sn-gantt-timeline-body
		const timelineComponent = getShadowRootHost(svg);

		scrollContainer = timelineComponent.shadowRoot.querySelector('.sn-gantt-timeline-body-wrapper');
		scrollContainerRect = scrollContainer.getBoundingClientRect();

		source = id;
		sourceTerminal = getTerminalName(event.currentTarget);

		const {
			width,
			left
		} = taskRect;

		const startPoint = {
			x: sourceTerminal === START ? left - awayFromTask : left + width + awayFromTask,
			y: yPos
		};

		dispatch(LINK_DRAG_STARTED, {
			source,
			sourceTerminal,
			x1: startPoint.x,
			y1: startPoint.y,
			x2: startPoint.x,
			y2: startPoint.y
		});

		handleMouseMove = (e) => {
			dragOk = true;

			throttledMouseMove({
				event: e,
				dispatch,
				svg,
				isRTL: isRTL
			});
		};

		handleMouseUp = (e) => {
			dragOk = false;

			onLinkDragMouseUp({
				event: e,
				dispatch
			});
		};

		window.addEventListener('mousemove', handleMouseMove);
		window.addEventListener('mouseup', handleMouseUp);
	}
};

export default onLinkDragStart;
