import { getTaskPosition } from './TaskManager';
import get from 'lodash/get';
import { MAX_BASELINE_TO_DISPLAY } from '../../constants';

/**
 * @param {Array} tasks - tasks prop
 * @param {object} timelineData
 */
export const getBaselinePositionMap = ({
	tasks,
	timelineData
}) => {
	const baselinePositionMap = {};

	tasks.forEach(task => {
		let isBaselineExists = false;
		let positions;

		if (Array.isArray(task.baselines)) {
			positions = task.baselines.slice(0, MAX_BASELINE_TO_DISPLAY).map(value => {
				return getTaskPosition({
					timelineData: timelineData,
					startDate: get(value, 'startDate'),
					endDate: get(value, 'endDate')
				});
			});

			if (positions.length > 0) {
				isBaselineExists = true;
			}
		}

		// If baseline positions are empty then don't store positions on the state
		if (isBaselineExists) {
			baselinePositionMap[task.id] = positions;
		}
	});

	return baselinePositionMap;
};
