import get from 'lodash/get';
import isArray from 'lodash/isArray';
import isFunction from 'lodash/isFunction';
import isEmpty from 'lodash/isEmpty';
import { getTaskPosition } from './TaskManager';
import { createLog } from '../../../util/logger';

const { error } = createLog('TaskLayerManager');

export const areValidTaskLayers = (taskLayers) => {
	if (isArray(taskLayers)) {
		taskLayers.forEach(layer => {
			const { columnName, startDate, endDate, renderer } = layer;

			if (isEmpty(columnName) || isEmpty(startDate) || isEmpty(endDate) || !isFunction(renderer)) {
				error('Not a valid task layer');
			}
		});
	} else {
		error('Task layer should be an Array');
	}

	return true;
};

/**
 * 
 * @param {Array} tasks - tasks prop
 * @param {object} timelineData
 * @param {Array} taskLayers
 */
export const getTaskLayerPositionMap = ({
	taskLayers,
	tasks,
	timelineData
}) => {
	const taskLayerPositionMap = {};

	// Task layers are availale then process the positions
	if (taskLayers.length > 0) {
		tasks.forEach(task => {
			let isTaskLayerExists = false;
			const result = {};

			taskLayers.forEach(layer => {
				if (isArray(task[layer.columnName])) {
					const positions = task[layer.columnName].map(value => {
						return getTaskPosition({
							timelineData: timelineData,
							startDate: get(value, layer.startDate),
							endDate: get(value, layer.endDate)
						});
					});

					if (positions.length > 0) {
						isTaskLayerExists = true;
					}

					result[layer.columnName] = positions;
				}
			});

			// If task layer positions are empty then don't store positions on the state
			if (isTaskLayerExists) {
				taskLayerPositionMap[task.id] = result;
			}
		});
	}

	return taskLayerPositionMap;
};
