import throttle from 'lodash/throttle';
import {
	RESIZE_END,
	TASK_DRAGGING_POSITION_UPDATED,
	THROTTLE_TIME,
	TASK_MIN_WIDTH,
	TASK_MIN_POS,
	RESIZE_START
} from '../constants';
import { getShadowRootHost } from '@devsnc/sn-datagrid';
import {
	canScrollEnd,
	setScrollEnd,
	canScrollStart,
	setScrollStart,
	cantScrollFurther
} from './TaskMoveManager';

let resizeOk = false;
let originalMouseX;
let left;
let width;
let scrollContainer;
let scrollContainerRect;
let leftScrollInterval;
let rightScrollInterval;
let initialScrollLeft;
let links = [];

const getUpdatedWidth = (width) => {
	return Math.max(width, TASK_MIN_WIDTH);
};
const getValidLeftPos = (leftPos, taskRect) => {
	const maxAllowedLeftPos = taskRect.left + taskRect.width - TASK_MIN_WIDTH;

	return Math.min(Math.max(leftPos, TASK_MIN_POS), maxAllowedLeftPos);
};
export const dispatchTaskUpdate = (dispatch, id, direction, isMoving = true) => {
	resizeOk && dispatch(TASK_DRAGGING_POSITION_UPDATED, {
		id,
		left,
		width,
		isMoving,
		direction,
		links

	});
};

const throttledResize = throttle(({
	event,
	dispatch,
	direction,
	id,
	taskRect,
	isRTL
}) => {
	if (!scrollContainer || !scrollContainerRect) {
		return;
	}
	let calcLeft = taskRect.left,
		calcWidth = taskRect.width;
	const prevScrollLeft = scrollContainer.scrollLeft;
	let scrollOffset = prevScrollLeft - initialScrollLeft;
	let mouseXDiff = event.clientX - originalMouseX;
	if (isRTL) {
		scrollOffset *= -1;
		mouseXDiff *= -1;
	}
	if (direction === RESIZE_END) {
		calcWidth = Math.min(
			getUpdatedWidth(taskRect.width + mouseXDiff + scrollOffset),
			scrollContainer.scrollWidth - taskRect.left - TASK_MIN_POS
		);
	} else {
		calcLeft = getValidLeftPos(taskRect.left + mouseXDiff + scrollOffset, taskRect);
		const leftOffset = taskRect.left - calcLeft;

		calcWidth = getUpdatedWidth(taskRect.width + leftOffset);
	}
	width = calcWidth;
	left = calcLeft;

	clearInterval(leftScrollInterval);
	clearInterval(rightScrollInterval);

	const scrollParams = {
		event,
		scrollContainer,
		scrollContainerRect,
		isRTL
	};

	if (canScrollEnd(scrollParams)) {
		rightScrollInterval = setInterval(() => {
			setScrollEnd(scrollParams);
			const incrementVal = Math.abs(scrollContainer.scrollLeft - prevScrollLeft);
			if (direction === RESIZE_END) {
				width = getUpdatedWidth(calcWidth + incrementVal);
			} else {
				left = getValidLeftPos(calcLeft + incrementVal, taskRect);
				const leftOffset = calcLeft - left;

				width = getUpdatedWidth(calcWidth + leftOffset);
			}

			dispatchTaskUpdate(dispatch, id, direction);
			if (!resizeOk || cantScrollFurther(scrollContainer, scrollContainerRect)) {
				clearInterval(rightScrollInterval);
			}
		}, THROTTLE_TIME);
	}

	if (canScrollStart(scrollParams)) {
		leftScrollInterval = setInterval(() => {
			setScrollStart(scrollParams);

			const incrementVal = Math.abs(scrollContainer.scrollLeft - prevScrollLeft);

			if (direction === RESIZE_START) {
				left = getValidLeftPos(calcLeft - incrementVal, taskRect);
				const leftOffset = calcLeft - left;

				width = getUpdatedWidth(calcWidth + leftOffset);
			} else {
				width = getUpdatedWidth(calcWidth - incrementVal);
			}

			dispatchTaskUpdate(dispatch, id, direction);
			if (!resizeOk || cantScrollFurther(scrollContainer, scrollContainerRect)) {
				clearInterval(leftScrollInterval);
			}
		}, THROTTLE_TIME);
	}

	dispatchTaskUpdate(dispatch, id, direction);
}, THROTTLE_TIME);

const onResizeStart = ({
	event,
	id,
	dispatch,
	direction,
	isRTL,
	taskRect,
	links: allLinks = []
}) => {
	originalMouseX = event.clientX;
	const timelineComponent = getShadowRootHost(event.currentTarget);

	scrollContainer = timelineComponent.shadowRoot.querySelector('.sn-gantt-timeline-body-wrapper');
	scrollContainerRect = scrollContainer.getBoundingClientRect();
	initialScrollLeft = scrollContainer.scrollLeft;
	resizeOk = true;

	// Before moving storing the links whose position has to be updated on task drag
	links = allLinks.filter(link => link.source === id || link.target === id);

	const handleMouseMove = (e) => {
		throttledResize({
			event: e,
			dispatch,
			direction,
			id,
			taskRect,
			isRTL
		});
	};

	const handleMouseUp = () => {
		// If mouse doesnt move then left, width is null, so not updating task position
		left && width && dispatchTaskUpdate(dispatch, id, direction, false);

		resizeOk = false;
		left = null;
		width = null;
		originalMouseX = null;
		initialScrollLeft = null;
		links = [];
		window.removeEventListener('mousemove', handleMouseMove);
		window.removeEventListener('mouseup', handleMouseUp);
		clearInterval(rightScrollInterval);
		clearInterval(leftScrollInterval);
	};

	window.addEventListener('mousemove', handleMouseMove);
	window.addEventListener('mouseup', handleMouseUp);
};

export default onResizeStart;
