import { VIRTUALIZATION_WINDOW_SIZE } from '../header/constants';
import { getDateFromPostion } from './TaskManager';
import { getScaleUnits, getTopScaleForBottomScale } from './TimelineManager';
import { DEFAULT_SCALES } from '../constants';
import { BOTTOM_SCALE_CHANGED } from '../../constants';

const defaultScaleData = { translate: 0, scales: [] };
const getIndex = (width, unitWidth) => width / unitWidth;
const getIndices = (scrollPos, windowSize, visibleWidth, width, scaleLength) => {
	const start = Math.floor(getIndex(scrollPos - windowSize, width) / 20) * 20;
	const end = Math.ceil(getIndex(scrollPos + visibleWidth + windowSize, width) / 20) * 20;

	return {
		start: start < 0 ? 0 : start,
		end: end > scaleLength ? scaleLength : end
	};
};
export const getDisplayScale = (state, dispatch) => {
	const { scaleCache } = state;
	const { timelineData, config, holidays } = state.properties;
	if (!timelineData) {
		return {
			topScaleData: defaultScaleData,
			bottomScaleData: defaultScaleData
		};
	}
	const {
		endDate: timelineEndDate,
		zoomLevel,
		visibleWidth,
		totalWidth,
		bottomScaleLength,
		displayOneScale
	} = timelineData;
	const { bottom } = zoomLevel.config;
	const windowSize = visibleWidth * VIRTUALIZATION_WINDOW_SIZE;

	// If time scale changes then scrollPos could be greater than totalWidth
	const scrollPos = Math.min(state.scrollPos, totalWidth - visibleWidth);
	const { start, end } = getIndices(scrollPos, windowSize, visibleWidth, bottom.width, bottomScaleLength);
	const key = `${start}-${end}`;
	let result = {};

	if (Object.prototype.hasOwnProperty.call(scaleCache, key)) {
		result = scaleCache[key];
	} else {
		const bottomUnitCount = end - start;
		const translate = start * bottom.width;
		const startPositionDate = getDateFromPostion({ timelineData, position: translate });
		const bottomScaleData = {
			translate,
			scales: getScaleUnits(startPositionDate, bottomUnitCount, bottom, config, holidays)
		};
		let topScaleData = defaultScaleData;
		if (!displayOneScale) {
			topScaleData = {
				translate,
				scales: getTopScaleForBottomScale(startPositionDate, timelineEndDate, zoomLevel.config, bottomUnitCount)
			};
			// Special handling for week
			if (zoomLevel.id === DEFAULT_SCALES.WEEK &&
				start === 0 &&
				topScaleData.scales[0].width === bottom.width
			) {
				topScaleData.scales[0].text = '';
			}
		}

		result = {
			topScaleData,
			bottomScaleData,
			scaleCache: {
				...scaleCache,
				[key]: {
					topScaleData,
					bottomScaleData
				}
			}
		};
	}

	dispatch(BOTTOM_SCALE_CHANGED, {
		bottomScaleData: result.bottomScaleData
	});

	return result;
};
