import { RTL } from '../../../constants';


const renderVerticalLines = ({
	timelineData: {
		zoomLevel: {
			config: {
				bottom: {
					width
				}
			}
		}
	},
	dir,
	bottomScaleData: { scales = [], translate = 0 }
}) => {
	return (
		<div
			className="timeline-body-layer sn-gantt-timeline-body-background"
			style={{
				transform: `translateX(${ dir === RTL ? -translate: translate}px)`
			}}
		>
			{
				scales.map((scale) => {
					const { holiday } = scale;

					return <div
						style={{ width: `${width}px` }}
						className={`sn-gantt-timeline-body-background-divs${holiday
							? ' sn-gantt-timeline-body-background-divs--holiday'
							: ''
						}`}
					></div>;
				})
			}
		</div>
	);
};

export default renderVerticalLines;
