import isEmpty from 'lodash/isEmpty';
import getFieldValue from '../../../helpers/getFieldValue';
import { FIELD_NAMES } from '../../../constants';

const { PERCENT_DONE } = FIELD_NAMES;

const renderProgress = ({
	task: {
		taskData
	},
	percentColors,
	isParent
}) => {
	const percentDone = getFieldValue(taskData, PERCENT_DONE) || 0;

	const classList = `sn-gantt-task-progress  ${
		isParent && percentDone > 0 ? 'sn-gantt-task-progress-left' : ''
	} ${
		isParent && percentDone === 100 ? 'sn-gantt-task-progress-right' : ''
	}`;
	const styles = {
		width: percentDone + '%'
	};
	const { backgroundColor } = percentColors;

	if (!isEmpty(backgroundColor)) {
		styles.backgroundColor = backgroundColor;
		styles.color = backgroundColor;
	}

	return (
		<div
			className={classList}
			style={styles}
		></div>
	);
};

export default renderProgress;
