import {
	TASK_MOUSE_ENTERED,
	TASK_MOUSE_LEFT
} from '../constants';
import { getTreeUtils } from '@devsnc/sn-datagrid';

const isChildDescendantOfParentId = (treeUtils, childTask, parentId, levelDiff) => {
	let currentTask = childTask,
		currentParent;
	while (levelDiff > 0) {
		currentParent = currentTask.getValue('parent');

		if (currentParent === parentId) {
			return true;
		}
		levelDiff--;
		currentTask = treeUtils.getNodeById(currentParent);
	}

	return false;
};
const isDirectDescendant = (sourceTaskId, targetTaskId, treeUtils) => {
	const sourceTask = treeUtils.getNodeById(sourceTaskId);
	const targetTask = treeUtils.getNodeById(targetTaskId);
	const levelDiff = targetTask.level - sourceTask.level;
	if (levelDiff === 0) {
		return false;
	}
	let childTask,
		parentId;
	if (levelDiff > 0) {
		childTask = targetTask;
		parentId = sourceTask.id;
	} else {
		childTask = sourceTask;
		parentId = targetTask.id;
	}

	return isChildDescendantOfParentId(treeUtils, childTask, parentId, Math.abs(levelDiff));
};

export const taksHoverActionHandlers = {
	[TASK_MOUSE_ENTERED]: {
		stopPropagation: true,
		effect({
			action: {
				payload: { id }
			},
			updateState,
			state,
			properties
		}) {
			const { source } = state.drawingLinkData;
			if (source !== null && source !== undefined) {
				if (isDirectDescendant(source, id, getTreeUtils(properties.tree))) {
					return;
				}
			}

			updateState({
				activeTaskId: id
			});
		}
	},
	[TASK_MOUSE_LEFT]: {
		stopPropagation: true,
		effect({
			updateState,
			state: { activeTaskId }
		}) {
			if (activeTaskId !== null && activeTaskId !== undefined) {
				updateState({
					activeTaskId: null
				});
			}
		}
	}
};
