import { createLog } from '../../util/logger';
import DateHelper from '../timeline/helpers/DateHelper';
const LOG = createLog('Timeline Date Calculator');
const { getNativeDate, getISODateTimeFormattedValue } = DateHelper;

export const getStartEndDate = ({ tasks, currentStartDate, currentEndDate }) => {
	const len = tasks.length;
	if (!tasks || len === 0) {
		return {
			startDate: currentStartDate,
			endDate: currentEndDate
		};
	}
	let startDate,
		endDate;
	if (currentStartDate && currentEndDate) {
		startDate = getNativeDate(currentStartDate);
		endDate = getNativeDate(currentEndDate);
	}
	for (let i = 0, start, end; i < len; i++) {
		try {
			const startDateObj = tasks[i].startDate;
			const endDateObj = tasks[i].endDate;

			start = getNativeDate(startDateObj.value);
			end = getNativeDate(endDateObj.value);
			if (!isNaN(start)) {
				startDate = startDate && startDate < start
					? startDate
					: start;
			} else {
				LOG.warn(`Invalid start date for task ${tasks[i].id}`);
			}
			if (!isNaN(end)) {
				endDate = endDate && endDate > end
					? endDate
					: end;
			} else {
				LOG.warn(`Invalid end date for task ${tasks[i].id}`);
			}
		} catch (e) {
			LOG.warn(`Invalid date for task ${tasks[i].id}`);
		}
	}

	return {
		startDate: getISODateTimeFormattedValue(startDate),
		endDate: getISODateTimeFormattedValue(endDate)
	};
};
