import { t } from 'sn-translate';
import { createLog } from '../../../../../util/logger';
import { LINK_INTERNAL_EVENT } from '../../../constants';

const LOG = createLog('sn-gantt-timeline-links');

/**
 * Fetches the Path information. Event.path is not directly available in Firefox.
 * @param {Object} event JavaScript Event
 */
const getPathId = (event, pathIndex = 0) => {
	const path = event.path || event.composedPath && event.composedPath();
	if (path) {
		return path[pathIndex].id;
	} else {
		LOG.error(t('This browser doesn\'t supply path information'));
	}
};

/**
 * Common Timeline Link Effect.
 * @param {String} eventName Dispatch event name.
 */
export const commonTimelineLinkEffect = ({ action: { payload: { event } }, dispatch }) => {
	event.preventDefault();

	const { x, y, type } = event;

	dispatch(LINK_INTERNAL_EVENT, {
		eventType: type,
		data: {
			id: getPathId(event),
			x,
			y
		}
	});
};

