import { actionTypes } from '@servicenow/ui-core';
import {
	GRID_RESIZED,
	GRID,
	LAYOUT_CHANGED,
	TIMELINE,
	NOW_GANTT_GRID_RESIZED,
	VIRTUAL_SCROLLBAR_HEIGHT,
	TOOLBAR_HEIGHT,
	GRID_RESIZER_GRIDVIEW,
	DEFAULT_CONFIG,
	DEFAULT,
	BAR_LABEL_TOGGLE
} from '../constants';
import {
	getTasksAndScrollheight,
	getColumnModels,
	getVisibleHeight,
	gridConstants,
	querySelector,
	getWhiteSpaceAdjustedColumns,
	getUpdatedTasksWithCurrentState,
	getCurrentGridWidth
} from '@devsnc/sn-datagrid';
import initTimelineConfig from '../timeline/helpers/TimelineConfig';
import getTransformedConfig from '../helpers/transformConfig';
import { getTimelineDataWithPositions } from '../timeline/helpers/TimelineManager';
import { getZoomLevels } from '../timeline/helpers/ZoomLevelManager';
import { areValidTaskLayers } from '../timeline/helpers/TaskLayerManager';
import { getBrowserScrollbarWidth } from '../helpers/ElementManager';
import { createLog } from '../../util/logger';
import get from 'lodash/get';
import debounce from 'lodash/debounce';
import isArray from 'lodash/isArray';
import { observedElementsMap, resizeObserver } from '../helpers/componentResize';
import { getStartEndDate } from '../helpers/minMaxDate';
import DateHelper from '../timeline/helpers/DateHelper';
import { scrollNewTaskIntoView } from '../helpers/taskHelper';
const LOG = createLog('componentActions');

const {
	COMPONENT_HOST_RESIZED,
	COLUMNS_INIT_WIDTH_AUTO_ADJUSTED,
	COLUMN_SORTED,
	COLUMNS_RESET
} = gridConstants;

const {
	COMPONENT_PROPERTY_CHANGED,
	COMPONENT_DOM_READY,
	COMPONENT_ERROR_THROWN,
	COMPONENT_CONNECTED,
	COMPONENT_DISCONNECTED
} = actionTypes;

// When vertical scroll is added, only `gridWidth`% of `scrollbarWidth` should be subtracted from gridwidth
export const getScrollBarWidthForGrid = (scrollbarWidth, gridWidth) => {
	return scrollbarWidth * gridWidth * 0.01;
};

export const getUpdatedTimelineDataAndPositionMaps = (state) => {
	const {
		config,
		timelineData,
		possibleZoomLevels,
		properties: {
			links,
			tasks
		},
		visibleWidth,
		tree,
		startDate,
		endDate
	} = state;

	const updatedTimelineData = getTimelineDataWithPositions({
		startDate,
		endDate,
		config,
		visibleWidth,
		multiplicationFactor: get(timelineData, 'zoomPercentage.multiplicationFactor'),
		possibleZoomLevels,
		links,
		tasks,
		tree
	});

	return updatedTimelineData;
};

const getUpdatedTaskData = ({
	sortBy: oldSortBy,
	state
}) => {
	const {
		columns,
		config,
		properties: {
			tasks: taskProp
		},
		tree: oldTree
	} = state;

	const {
		scrollHeight,
		taskExpandMap,
		sortBy,
		tree
	} = getTasksAndScrollheight({
		config,
		columns: columns,
		tasks: taskProp,
		sortBy: oldSortBy,
		taskExpandMap: oldTree.taskExpandMap
	});

	const oldTasks = oldTree.tasks || [];
	const selectedTasks = oldTasks.filter(task => task.selected);
	const updatedTasks = getUpdatedTasksWithCurrentState({
		...state,
		tree,
		selectedTasks
	});

	return {
		tree,
		scrollHeight,
		taskExpandMap,
		sortBy,
		...updatedTasks
	};
};

const initializeColumns = ({ config, dispatch, scrollbarWidth, columns }) => {
	const portionOfScrollbarInGrid = getScrollBarWidthForGrid(scrollbarWidth, config.gridWidth);

	const gridColumns = getColumnModels({ columns, config: {
		readonly: config.readonly,
		editMode: config.editMode
	} });

	dispatch(COLUMNS_INIT_WIDTH_AUTO_ADJUSTED, {
		scrollbarWidth: portionOfScrollbarInGrid
	});

	return gridColumns;
};

const initializeTimeline = (params) => {
	const { tConfig, visibleWidth, state, tree } = params;
	let { startDate, endDate } = params;
	const possibleZoomLevels = getZoomLevels(tConfig.scales);

	// Validating and setting moment constants
	areValidTaskLayers(tConfig.taskLayers);
	initTimelineConfig({
		dateFormat: tConfig.dateFormat,
		dateTimeFormat: tConfig.dateTimeFormat,
		timeZone: tConfig.timeZone,
		weekStartsOn: tConfig.weekStartsOn
	});

	if (!startDate && !endDate) {
		const updatedDates = getStartEndDate({
			tasks: state.properties.tasks,
			config: tConfig
		});

		startDate = updatedDates.startDate;
		endDate = updatedDates.endDate;
	}

	// Using calculated dimensions to generate timelineData and adjust for column whitespace
	const { timelineData, positionMaps } = getUpdatedTimelineDataAndPositionMaps({
		...state,
		tree,
		config: tConfig,
		possibleZoomLevels,
		visibleWidth,
		startDate,
		endDate
	});

	return {
		timelineData,
		positionMaps,
		possibleZoomLevels,
		startDate,
		endDate
	};
};

const getGanttDimesions = ({ tConfig, host }) => {
	// DOM caclulations for visibleHeight, gridWidth, timelineWidth, scrollbarWidth
	const { headerHeight, toolbar, gridWidth: width } = tConfig;
	const bodyContainer = host.shadowRoot.querySelector('.now-gantt-container');
	const containerWidth = bodyContainer.getBoundingClientRect().width;
	const gridWidth = containerWidth*width/100;
	const visibleWidth = containerWidth - gridWidth;
	const scrollbarWidth = getBrowserScrollbarWidth(host);
	let visibleHeight = getVisibleHeight(bodyContainer);

	visibleHeight = visibleHeight - VIRTUAL_SCROLLBAR_HEIGHT - headerHeight; // Header column's row height
	if (toolbar && toolbar.display) {
		visibleHeight -= TOOLBAR_HEIGHT;
	}

	return {
		gridWidth,
		visibleWidth,
		scrollbarWidth,
		visibleHeight
	};
};

const handleDOMReady = ({
	host,
	state,
	updateState,
	dispatch
}) => {
	const { properties } = state;
	const {
		config,
		sortBy: propSortBy,
		columns,
		holidays
	} = properties;
	const startDate = properties.startDate;
	const endDate = properties.endDate;

	const tConfig = getTransformedConfig(config);

	const {
		visibleWidth,
		scrollbarWidth,
		visibleHeight,
		gridWidth
	} = getGanttDimesions({ tConfig, host });

	const gridColumns = initializeColumns({
		config: tConfig,
		columns,
		dispatch,
		scrollbarWidth
	});

	const { scrollHeight, sortBy, tree } = getUpdatedTaskData({
		state: {
			...state,
			config: tConfig,
			columns: gridColumns.columns
		},
		sortBy: propSortBy
	});

	const timelineInitData = initializeTimeline({
		tConfig,
		scrollbarWidth,
		visibleWidth,
		startDate,
		endDate,
		state,
		tree
	});

	let layout = DEFAULT;
	if (properties.hideTimeline) {
		tConfig.gridWidth = GRID_RESIZER_GRIDVIEW;
		tConfig.showBarLabel = false;
		layout = GRID;
	}

	// transforming holidays into moment dates
	const momentHolidays = holidays.map((holiday) => DateHelper.getFormattedValue(holiday, tConfig.dateFormat));


	updateState({
		...gridColumns,
		config: tConfig,
		tree,
		...timelineInitData,
		scrollHeight,
		sortBy,
		visibleHeight,
		visibleWidth,
		gridWidth,
		scrollbarWidth,
		layout,
		holidays: momentHolidays
	});
};

const handleTasksandLinksChange = ({
	state,
	updateState,
	tasks
}) => {
	const { sortBy, config } = state;
	const { startDate, endDate } = state.properties;
	let updatedDates = {};
	const updatedTaskData = getUpdatedTaskData({
		state,
		sortBy
	});
	const { tree } = updatedTaskData;
	if (!startDate && !endDate) {
		updatedDates = getStartEndDate({
			tasks,
			currentStartDate: state.startDate,
			currentEndDate: state.endDate,
			config
		});
	}
	// TODO: if tasks/links/dates are updated at once then dont recalc posMaps on each prop change
	// if only links are updated then only linkPositionMap needs to be recalculated instead of all maps
	const updatedTimelineData = getUpdatedTimelineDataAndPositionMaps({
		...state,
		...updatedDates,
		tree
	});

	updateState({
		...updatedTaskData,
		...updatedTimelineData,
		...updatedDates
	});
};


export default {
	[COMPONENT_ERROR_THROWN]: ({
		action: {
			payload
		}
	}) => {
		// This is mandatory to find the issues on production instance
		LOG.log(payload);
	},
	[COMPONENT_DOM_READY]: handleDOMReady,
	[COMPONENT_PROPERTY_CHANGED]: ({
		host,
		action: { payload },
		updateState,
		state,
		properties,
		dispatch
	}) => {
		const { name, value } = payload;
		switch (payload.name) {
			case 'startDate':
			case 'endDate':
				/**
				 * TODO: if startDate/endDate/tasks/links are updated at once
				 * then dont recalculate positionMaps for each prop change, do it only once
				 */
				updateState({
					...getUpdatedTimelineDataAndPositionMaps({
						...state,
						[name]: value
					}),
					[name]: value
				});
				break;

			case 'sortBy': {
				const { name, order } = value;

				dispatch(COLUMN_SORTED, { name, order, notifyConsumer: false });
				break;
			}

			case 'links': {
				handleTasksandLinksChange({
					state,
					updateState,
					tasks: properties.tasks
				});
				break;
			}

			case 'tasks': {
				handleTasksandLinksChange({
					state,
					updateState,
					tasks: value
				});
				scrollNewTaskIntoView(host, value);
				break;
			}

			case 'hideTimeline': {
				const ganttContainerRef = {
					current: host.shadowRoot.querySelector('.sn-gantt')
				};
				const layout = value ? GRID : DEFAULT;
				const configGridWidth = value
					? GRID_RESIZER_GRIDVIEW
					: get(properties, 'config.gridWidth', DEFAULT_CONFIG.gridWidth);

				dispatch(LAYOUT_CHANGED, {
					ganttContainerRef,
					width: configGridWidth,
					layout
				});
				break;
			}


			case 'columns': {
				/**
				 * if column property changes then we will remove the
				 * 1. sort
				 * 2. filter
				 * 3. column attibute changes
				 */
				const { scrollbarWidth, config } = state;
				const portionOfScrollbarInGrid = getScrollBarWidthForGrid(scrollbarWidth, config.gridWidth);

				dispatch(COLUMNS_RESET, {
					columns: value,
					scrollbarWidth: portionOfScrollbarInGrid
				});
				break;
			}

			case 'holidays': {
				const { config } = state;
				const newHoliday = isArray(value) ? value : [];

				updateState({
					holidays: newHoliday.map((holiday) => DateHelper.getFormattedValue(holiday, config.dateFormat))
				});
			}
		}
		/**
		 * TODO: seismic@20 will add an accumulator with debounce for prop change handler
		 * use it to calculate position maps only once, instead of calculating on every prop change
		 */
	},
	[COMPONENT_HOST_RESIZED]: {
		modifier: { name: 'debounce', limit: 50 },
		effect({
			host,
			state,
			updateState,
			action: {
				payload = {}
			}
		}) {
			const { type } = payload;
			const {
				visibleWidth: oldVisibleWidth,
				layout,
				columns,
				scrollbarWidth
			} = state;
			const updatedParams = {};

			if (layout !== GRID) {
				const visibleWidth = querySelector(host, 'sn-gantt-timeline-header').getBoundingClientRect().width;
				if (oldVisibleWidth !== visibleWidth) {
					updatedParams.visibleWidth = visibleWidth;
					const { timelineData, positionMaps } = getUpdatedTimelineDataAndPositionMaps({
						...state, visibleWidth
					});

					updatedParams.timelineData = timelineData;
					updatedParams.positionMaps = positionMaps;
				}
			}

			if (layout !== TIMELINE) {
				let gridWidth = querySelector(host, '.sn-datagrid-body').getBoundingClientRect().width;
				const scrollWidth = state.scrollHeight > state.visibleHeight
					? scrollbarWidth
					: 0;

				gridWidth = gridWidth + scrollWidth;
				updatedParams.gridWidth = gridWidth;
				const updatedGridWidth = getCurrentGridWidth({
					...state,
					gridWidth,
					scrollbarWidth: scrollWidth
				});

				updatedParams.columns = getWhiteSpaceAdjustedColumns({
					...state,
					gridWidth: updatedGridWidth,
					columns
				});
			}

			if (type !== GRID_RESIZED) {
				updatedParams.visibleHeight = getVisibleHeight(
					host.shadowRoot.querySelector('.sn-gantt-body-container')
				);
			}

			updateState({
				...updatedParams
			});
		}
	},
	[GRID_RESIZED]: {
		modifier: { name: 'debounce', limit: 50 },
		effect({
			action: { payload: { width } },
			dispatch,
			updateState
		}) {
			updateState({
				path: 'config.gridWidth',
				value: width,
				operation: 'set'
			});
			dispatch(() => {
				return {
					type: COMPONENT_HOST_RESIZED,
					payload: {
						type: GRID_RESIZED
					}
				};
			});

			dispatch(NOW_GANTT_GRID_RESIZED, {
				width
			});
		}
	},
	[LAYOUT_CHANGED]: ({
		action: { payload: { width, ganttContainerRef, layout } },
		state,
		updateState,
		dispatch
	}) => {
		const {
			properties,
			config,
			columns,
			visibleWidth: oldVisibleWidth,
			scrollbarWidth,
			config: { showBarLabel },
			layout: prevLayout
		} = state;

		const containerWidth = ganttContainerRef.current.getBoundingClientRect().width;
		const gridWidth = containerWidth*width/100;
		const visibleWidth = containerWidth - gridWidth;

		const updatedState = {
			layout,
			gridWidth,
			columns: getWhiteSpaceAdjustedColumns({
				gridWidth: getCurrentGridWidth({
					...state,
					gridWidth,
					scrollbarWidth: getScrollBarWidthForGrid(scrollbarWidth, width)
				}),
				columns
			})
		};

		if (layout !== GRID && oldVisibleWidth !== visibleWidth) {
			updatedState.visibleWidth = visibleWidth;
			const { timelineData, positionMaps } = getUpdatedTimelineDataAndPositionMaps({
				...state, visibleWidth
			});

			updatedState.timelineData = timelineData;
			updatedState.positionMaps = positionMaps;
		}
		updateState({
			...updatedState,
			config: {
				...config,
				gridWidth: width
			}
		});

		if (layout === GRID && showBarLabel) {
			dispatch(BAR_LABEL_TOGGLE);
		} else if (prevLayout === GRID && layout !== GRID && !showBarLabel && get(properties, 'config.showBarLabel')) {
			dispatch(BAR_LABEL_TOGGLE);
		}
	},
	[COMPONENT_CONNECTED]: {
		effect: ({ dispatch, action: { payload: { host } } }) => {
			const dispatchHostResized = debounce(() => {
				dispatch(COMPONENT_HOST_RESIZED, {
					host
				});
			}, 300);

			resizeObserver.observe(host);
			observedElementsMap.set(host, dispatchHostResized);
		},
		stopPropagation: true
	},
	[COMPONENT_DISCONNECTED]: {
		effect: ({ action: { payload } }) => {
			resizeObserver.unobserve(payload.host);
			observedElementsMap.delete(payload.host);
		},
		stopPropagation: true
	}
};
