export default class Scale {
	constructor({
		format,
		rangeUnit,
		step = 1
	}) {
		if (!format) {
			throw new Error('Format should be specified');
		}

		this.format = format;

		if (!rangeUnit) {
			throw new Error('Range unit should be specified');
		}

		this.rangeUnit = rangeUnit;
		this.step = step;
	}
}
