import { DISPLAY_BAR_POPOVER, HIDE_BAR_POPOVER } from '../../constants';
import { COMPONENT_RENDERED, COMPONENT_PROPERTY_CHANGED, COMPONENT_DOM_READY } from '@servicenow/ui-core/lib/constants';
import { TIMELINE_SECTION, TIMELINE_BODY_ITEM_SELECTED, RTL } from '../../../constants';
import get from 'lodash/get';
import { renderPopover } from '../../helpers/barPopover';
import { getDependencyPathStrings } from '../../helpers/LinkManager';

export const timelineBodyActions = {
	[COMPONENT_PROPERTY_CHANGED]: {
		effect({
			state: { timelinePopover },
			action: { payload },
			properties: {
				timelineData,
				visibleTasks,
				dir,
				columns,
				config,
				positionMaps,
				tree
			},
			updateState,
			dispatch
		}) {
			const { taskPositionMap, linkPositionMap } = positionMaps;
			const { host, name, value, previousValue } = payload;
			const { totalWidth, visibleWidth, zoomLevel } = timelineData;
			const width = get(zoomLevel, 'config.bottom.width', 50);

			// To redraw dependencies
			if (
				name === 'config' && value.rowHeight !== previousValue.rowHeight ||
				name === 'positionMaps' ||
				name === 'tree'
			) {
				const { rowHeight, dependencyRadius } = config;

				updateState({
					dependencyLinePaths: getDependencyPathStrings(linkPositionMap, tree, rowHeight, dependencyRadius)
				});
			}
			if (name === 'selectedCell' && value && value.rowIndex !== 0) {
				const { timelinePopoverConfig = {} } = config;
				const { displayPopover = true } = timelinePopoverConfig;
				const task = visibleTasks.find(task => task.index === value.rowIndex - 1);
				const isRTL = dir === RTL;
				if (task) {
					const taskRect = taskPositionMap[task.id];
					if (taskRect) {
						if (value.columnId !== TIMELINE_SECTION) {
						// If arrow navigating in grid, scroll timeline bar into view
							let scrollPos = taskRect.left - width;

							if (isRTL) {
								scrollPos = totalWidth - scrollPos - visibleWidth;
							}
							const timelineWrapper = host.shadowRoot.querySelector('.sn-gantt-timeline-body-wrapper');

							timelineWrapper.scrollTo(scrollPos, 0);
						} else {
							displayPopover && renderPopover({
								host,
								task,
								taskRect,
								columns,
								timelinePopoverConfig,
								dispatch,
								isRTL
							});
						}
					}
				}
			}
			if (timelinePopover !== null) {
				updateState({ timelinePopover: null });
			}
		}
	},
	[COMPONENT_DOM_READY]: ({
		action: {
			payload: {
				host
			}
		},
		properties: {
			config,
			positionMaps,
			tree,
			columns
		},
		updateState
	}) => {
		// Firefox: Remembers scroll position causes timeline header & body mismatch issue
		host.scrollTo(0, 0);
		const { linkPositionMap } = positionMaps;
		const { rowHeight, dependencyRadius } = config;
		const textTypeColumn = columns.find(column => column.type === 'text');

		updateState({
			dependencyLinePaths: getDependencyPathStrings(
				linkPositionMap, tree, rowHeight, dependencyRadius
			),
			... textTypeColumn && { textTypeColumnName: textTypeColumn.name }
		});
	},
	[COMPONENT_RENDERED]: ({
		action: { payload: { host } },
		properties: { selectedCell, isScrollingVertically }
	}) => {
		if (selectedCell && selectedCell.columnId === TIMELINE_SECTION) {
			let currentElement;
			if (selectedCell.rowIndex !== 0) {
				currentElement = host.shadowRoot.querySelector(`.${TIMELINE_BODY_ITEM_SELECTED}`);
				currentElement && currentElement.focus({ preventScroll: isScrollingVertically });
			}
		}
	},
	[HIDE_BAR_POPOVER]: {
		modifier: { name: 'debounce', delay: 100 },
		stopPropagation: true,
		effect({
			updateState,
			state: { timelinePopover }
		}) {
			if (timelinePopover !== null) {
				updateState({ timelinePopover: null });
			}
		}
	},
	[DISPLAY_BAR_POPOVER]: {
		stopPropagation: true,
		effect({
			updateState,
			action: {
				payload
			}
		}) {
			updateState({
				timelinePopover: payload
			});
		}
	}
};
