import isEmpty from 'lodash/isEmpty';
import debounce from 'lodash/debounce';
import {
	DISPLAY_BAR_POPOVER,
	HIDE_BAR_POPOVER,
	DEFAULT_BAR_POPOVER_CONFIG,
	BAR_INFO_ITEM_HEIGHT,
	BAR_INFO_TITLE_HEIGHT,
	BAR_INFO_PADDING
} from '../constants';
import { FIELD_NAMES, DEFAULT_FIELD_LABELS } from '../../constants';

export const destroyPopover = ({ dispatch }) => {
	displayPopover.cancel(); //eslint-disable-line
	dispatch(HIDE_BAR_POPOVER);
};

const displayPopover = debounce(({
	dispatch,
	fields,
	titleField,
	columns,
	task,
	popoverWidth,
	taskRect: {
		left
	},
	mousePos,
	scrollContainer,
	scrollContainerRect,
	isRTL
}) => {
	const fieldsList = fields.replace(/\s/g, '').split(',').slice(0, 5)
		.filter(field => {
			const label = columns.find(col => {
				return col.name === field;
			});

			return !isEmpty(label) ||
				field === FIELD_NAMES.START_DATE ||
				field === FIELD_NAMES.END_DATE ||
				field === FIELD_NAMES.PERCENT_DONE;
		})
		.map(field => {
			let label,
				value;
			const column = columns.find(col => {
				return col.name === field;
			});

			// If there are no date columns, assign the default label and use taskNode's value getter
			if (isEmpty(column)) {
				label = DEFAULT_FIELD_LABELS[field];
				value = task.getValue(`${field}.displayValue`) || '';
			} else {
				label = column.label;
				// TODO: export `getColumnModelFromId()` from grid and use columnModels display value getter below
				value = task.getValue(`${field}.displayValue`) || '';
			}
			// Removing time component, as a fallback
			if (column && column.type === 'date') {
				value = value.split(' ')[0];
			}

			return {
				label,
				value
			};
		});

	const { width, left: containerLeft } = scrollContainerRect;
	const { scrollLeft, scrollWidth } = scrollContainer;
	const text = task.getValue(`${titleField}.displayValue`);
	let popoverHeight = fieldsList.length * BAR_INFO_ITEM_HEIGHT + BAR_INFO_PADDING;
	if (text) {
		popoverHeight += BAR_INFO_TITLE_HEIGHT;
	}
	let horizontalPos = isRTL ? scrollWidth - left: left;
	if (mousePos) {
		horizontalPos = mousePos - containerLeft + scrollLeft;
	}
	if (isRTL) {
		horizontalPos = Math.min(
			Math.max(horizontalPos, scrollLeft + 10 + popoverWidth),
			scrollLeft + width - 10
		) - scrollWidth;
	} else {
		horizontalPos = Math.min(
			Math.max(horizontalPos, scrollLeft + 10),
			scrollLeft + width - popoverWidth - 10
		);
	}

	dispatch(DISPLAY_BAR_POPOVER, {
		text,
		fields: fieldsList,
		index: task.index,
		left: horizontalPos,
		width: popoverWidth,
		height: popoverHeight,
		id: task.id
	});
}, 300);

export const renderPopover = ({
	host,
	mousePos,
	taskRect,
	task,
	timelinePopoverConfig: {
		titleField = DEFAULT_BAR_POPOVER_CONFIG.titleField,
		fields = DEFAULT_BAR_POPOVER_CONFIG.fields,
		popoverWidth = DEFAULT_BAR_POPOVER_CONFIG.popoverWidth
	},
	columns,
	dispatch,
	isRTL
}) => {
	const scrollContainer = host.shadowRoot.querySelector('.sn-gantt-timeline-body-wrapper');
	const scrollContainerRect = scrollContainer.getBoundingClientRect();

	displayPopover({
		dispatch,
		fields,
		columns,
		task,
		popoverWidth,
		taskRect,
		mousePos,
		scrollContainer,
		scrollContainerRect,
		isRTL,
		titleField
	});
};
