import { RTL } from '../../../../constants';
import { PROJECT_START, PROJECT_END } from '../../../../../constants/translated';
import DateHelper from '../../../helpers/DateHelper';
import { getTaskWidth } from '../../../helpers/TaskManager';
import { Fragment } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line

const {
	getISODateTimeFormattedValue,
	getDate,
	getToday
} = DateHelper;

const _getTaskWidth = ({
	startDate,
	endDate,
	unit,
	width,
	step
}) => {
	endDate = endDate ? endDate : getDate();

	return getTaskWidth(
		getISODateTimeFormattedValue(startDate),
		getISODateTimeFormattedValue(endDate),
		unit,
		width,
		step
	);
};

const renderLine = (width, text, dir) => {
	return (
		<div
			className="dotted_line"
			style={{
				transform: `translate(${dir === RTL ? '-' : ''}${width}px, 0)`
			}}
		>
			<div>{text}</div>
		</div>
	);
};

const renderCurrentTimeline = ({ startDate, endDate, unit, width, dir, step }) => {
	const today = getToday();

	if (today > getDate(endDate) || today < getDate(startDate)) {
		return <Fragment />;
	}

	const awayFrom = _getTaskWidth({
		startDate,
		endDate: today,
		unit,
		width,
		step
	});

	return (
		<div
			className="current_date_line"
			style={{
				transform: `translate(${dir === RTL ? '-' : ''}${awayFrom}px, 0)`
			}}>
		</div>
	);
};

const renderStartTimeline = ({ startDate, endDate, unit, width, step, dir }) => {
	const projectStart = _getTaskWidth({
		startDate,
		endDate,
		unit,
		width,
		step
	});

	return renderLine(projectStart, PROJECT_START, dir);
};

const renderEndTimeline = ({ startDate, endDate, unit, width, step, dir }) => {
	const projectEnd = _getTaskWidth({
		startDate,
		endDate,
		unit,
		width,
		step
	});

	return renderLine(projectEnd, PROJECT_END, dir);
};

const renderTimelines = ({
	timelineData: {
		startDate,
		endDate,
		zoomLevel: {
			config: {
				bottom: {
					rangeUnit: unit,
					width,
					step
				}
			}
		}
	},
	config: {
		hideCurrentTimeline,
		showStartEndTimeline
	},
	dir
}) => {
	return (
		<Fragment>
			{showStartEndTimeline
				? renderStartTimeline({
					startDate,
					endDate: startDate,
					unit,
					width,
					step,
					dir
				})
				: <Fragment />}
			{hideCurrentTimeline
				? <Fragment />
				: renderCurrentTimeline({
					startDate,
					endDate,
					unit,
					width,
					dir,
					step
				})}
			{showStartEndTimeline
				? renderEndTimeline({
					startDate,
					endDate: endDate,
					unit,
					width,
					step,
					dir
				})
				: <Fragment />}
		</Fragment>
	);
};

export default renderTimelines;
