import { getGridConfig } from '@devsnc/sn-datagrid';
import { DEFAULT_CONFIG } from '../constants';
import { TIME_UNITS } from '../timeline/constants';
import get from 'lodash/get';

const getTransformedConfig = (config = {}) => {
	const tConfig = getGridConfig({
		...DEFAULT_CONFIG,
		...config
	});

	if (tConfig.showBarLabel) {
		tConfig.rowHeight += 10;
	}

	if (tConfig.gridWidth > 85 || tConfig.gridWidth < 15) {
		tConfig.gridWidth = DEFAULT_CONFIG.gridWidth;
	}

	// TODO: handle precision for every timescale individually
	tConfig.precision = get(config, 'timeScalePrecision.day') === TIME_UNITS.DAY
		? TIME_UNITS.DAY
		: TIME_UNITS.MILLISECOND;

	return tConfig;
};

export default getTransformedConfig;
