import DateHelper from '../helpers/DateHelper';
import {
	calcStartDate,
	calcEndDate,
	getAdjustedEndDate,
	getScaleWidth,
	getWidthAdjustedFormat
} from '../helpers/TimelineManager';
import { calculateAutoFitScale } from '../helpers/AutoFitManager';
import cloneDeep from 'lodash/cloneDeep';
import performanceLogger from '../../../util/performanceLogger';
import { createLog } from '../../../util/logger';
import { TIME_UNITS, TWO_CHAR_WIDTH, FOUR_CHAR_WIDTH, DEFAULT_SCALES } from '../constants';
import { ZOOM_IN_ZOOM_OUTS, DEFAULT_ZOOM_LEVELS } from '../default/zoomLevels';
import { findZoomLevel } from '../helpers/ZoomLevelManager';
import { findZoomPercentage } from '../helpers/ZoomPercentageManager';
import { DEFAULT_ZOOM_IN_LEVEL } from '../../constants';

const { getToday, getISODateFormattedValue, getNextUnit, getNativeDiff, getStartOfUnit } = DateHelper;

/**
 * 1. Find the zoom level && set zoomLevel && zoomInLevel
 * 2. If zoomLevel = autofit then
 * 		2.1 Calculate the best possible scale & zoom in level
 * 3. Update the Bottom Scale width based on current multiplication factor
 * 4. Get the new bottom scale width for autofit and additional units to be added for other timescales
 * 5. Find the start and end dates based on zoomLevel config, and adjust bottom format based on width
 * 6. Calculate the top & bottom scale length and unit Widths
 */
export default class Timeline {
	constructor({
		zoomLevel = DEFAULT_SCALES.DAY,
		startDate = getISODateFormattedValue(getToday()),
		endDate = getISODateFormattedValue(getToday()),
		visibleWidth = 0,
		taskHeight,
		multiplicationFactor = DEFAULT_ZOOM_IN_LEVEL,
		possibleZoomLevels = DEFAULT_ZOOM_LEVELS,
		precision = TIME_UNITS.MILLISECOND
	}) {
		let _zoomLevel = findZoomLevel(possibleZoomLevels, zoomLevel);

		if (!_zoomLevel) {
			_zoomLevel = findZoomLevel(DEFAULT_ZOOM_LEVELS, DEFAULT_SCALES.AUTOFIT);
			const LOG = createLog('componentActions');

			LOG.error('Invalid possibleZoomLevels', possibleZoomLevels, 'or invalid zoomLevel.', zoomLevel, 'Using autofit instead.');
		}

		this.zoomLevel = cloneDeep(_zoomLevel);
		this.zoomPercentage = findZoomPercentage(multiplicationFactor);
		this.visibleWidth = visibleWidth;
		this.taskHeight = taskHeight;
		this.precision = precision;

		// If start date is before end date, change end date to one unit after start date
		if (getNativeDiff(startDate, endDate, this.zoomLevel.config.bottom.rangeUnit) < 0) {
			endDate = getISODateFormattedValue(getNextUnit(startDate, this.zoomLevel.config.bottom.rangeUnit));
		}

		/** 
		 * Step 2
		*/
		if (this.zoomLevel.id === DEFAULT_SCALES.AUTOFIT) {
			const { zoomLevel, zoomPercentage } = performanceLogger(calculateAutoFitScale)({
				startDate,
				endDate,
				visibleWidth,
				zoomInZoomOuts: ZOOM_IN_ZOOM_OUTS
			});

			this.zoomPercentage = zoomPercentage;
			this.zoomLevel.config = cloneDeep(findZoomLevel(possibleZoomLevels, zoomLevel)).config;
		}

		/** 
		 * Step 3
		*/
		this.zoomLevel.config.bottom.width *= this.zoomPercentage.multiplicationFactor;

		/** 
		 * Step 4
		*/
		if (this.zoomLevel.id !== DEFAULT_SCALES.AUTOFIT) {
			endDate = getAdjustedEndDate({
				startDate,
				endDate,
				visibleWidth,
				zoomLevel: this.zoomLevel
			});
		} else {
			this.zoomLevel.config.bottom.width = getScaleWidth({
				startDate,
				endDate,
				visibleWidth,
				zoomLevel: this.zoomLevel
			});
		}

		const {
			config: {
				bottom,
				top: {
					rangeUnit: topLevelUnit
				}
			},
			extraUnits
		} = this.zoomLevel;
		const {
			rangeUnit: bottomUnit,
			width: bottomUnitWidth,
			step: bottomStep
		} = bottom;

		/** 
		 * Step 5
		*/
		this.zoomLevel.config.bottom.format = getWidthAdjustedFormat(bottom);
		this.startDate = calcStartDate({
			startDate,
			rangeUnit: bottomUnit,
			extraUnits
		});
		this.endDate = calcEndDate({
			endDate,
			rangeUnit: bottomUnit,
			extraUnits
		});


		// Special handling for week
		if (this.zoomLevel.id === DEFAULT_SCALES.WEEK) {
			// First unit should be at the start of the week
			const startOfWeek = getStartOfUnit(startDate, TIME_UNITS.WEEK);
			if (getNativeDiff(startOfWeek, startDate, TIME_UNITS.DAY) >= 1) {
				this.startDate = getISODateFormattedValue(startOfWeek);
			}
			if (bottomUnitWidth < TWO_CHAR_WIDTH) {
				this.zoomLevel.config.bottom.format = 'dd';
			} else if (bottomUnitWidth < FOUR_CHAR_WIDTH) {
				this.zoomLevel.config.bottom.format = 'ddd';
			} else {
				this.zoomLevel.config.bottom.format = 'dddd';
			}
		}

		/** 
		 * Step 6
		*/
		this.bottomScaleLength = Math.ceil(getNativeDiff(this.startDate, this.endDate, bottomUnit)) / bottomStep;
		this.displayOneScale = topLevelUnit === bottomUnit;
		this.totalWidth = this.bottomScaleLength * bottomUnitWidth;
	}
}
