// eslint-disable-next-line no-unused-vars
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom';
import renderTimelines from './timelines';
import { RTL } from '../../../../constants';

const { createElementFromNode } = snabbdom;

const getLinks = ({
	dir,
	totalWidth,
	dependencyLinePaths
}) => {
	const isRTL = dir === RTL;
	const linkArray = [];

	for (let index = 0; index < dependencyLinePaths.length; index++) {
		const { linkId, pointString } = dependencyLinePaths[index];

		linkArray.push(
			`<path
			class="sn-gantt-timeline-links-dependency"
			d="${pointString}"
			id="${linkId}_background"
			marker-end="url(#arrowEnd)"
			transform="translate(${isRTL ? totalWidth : 0}, 0) scale(${isRTL ? '-' : ''}1,1)">
			</path>`
		);

		linkArray.push(
			`<path
			class="sn-gantt-timeline-links-dependency sn-gantt-timeline-links-dependency--hover-links"
			d="${pointString}"
			id="${linkId}"
			transform="translate(${isRTL ? totalWidth : 0}, 0) scale(${isRTL ? '-' : ''}1,1)">
			</path>`
		);
	}

	return linkArray;
};

const generateSvg = (linkGenerationParams) => {
	const wrapper = document.createElement('SPAN');

	wrapper.innerHTML = `<svg
		xmlns="http://www.w3.org/2000/svg"
		class="sn-gantt-timeline-links"
		width="100%" height="100%" aria-hidden="true">
		<defs>
			<marker
				class="sn-gantt-timeline-links-dependency sn-gantt-timeline-links-arrow-head"
				id="arrowEnd"
				markerWidth="10"
				markerHeight="10"
				refX="11"
				refY="8"
				viewBox="0 0 16 16"
				orient="auto">
				<path d="M6 3.5L11 8l-5 4.5z"></path>
			</marker>
		</defs>
		${getLinks(linkGenerationParams).join('')}
	</svg>`;

	const vnode = createElementFromNode(wrapper);

	return vnode.children[0];
};

const renderLinks = (linkGenerationParams) => {
	return <div>
		{ linkGenerationParams.displayDependencyLines
			? generateSvg(linkGenerationParams)
			: <Fragment/>
		}
		{ renderTimelines(linkGenerationParams) }
	</div>;
};

export default renderLinks;
