import isEmpty from 'lodash/isEmpty';
import get from 'lodash/get';
import noop from 'lodash/noop';
import '@servicenow/now-icon';
import { Fragment, createElement } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line
import { renderPopover, destroyPopover } from '../../helpers/barPopover';
import {
	RTL,
	TIMELINE_SECTION,
	TIMELINE_BODY_ITEM_SELECTED,
	TAB_KEY
} from '../../../constants';
import templates from '../../../templates';
import onDragStart from '../../helpers/TaskMoveManager';
import {
	TASK_MOUSE_ENTERED,
	TASK_MOUSE_LEFT,
	RESIZE_START,
	RESIZE_END,
	TASK_TYPES,
	TASK_ACTIONS_CONTAINER_WIDTH,
	DEPDENCY_CIRCLE_AND_TASK_RESIZER_WIDTH,
	GAP_BETWEEN_DEPENDENCY_CIRCLE_AND_TASK_RESIZER,
	TASK_BAR_RIGHT_CLICKED_INTERNAL,
	TASK_BAR_CLICKED_INTERNAL,
	HIDE_BAR_POPOVER,
	LINKS,
	START,
	FINISH
} from '../../constants';
import onResizeStart from '../../helpers/TaskResizeManager';
import onLinkDragStart, { onLinkDragMouseUp } from '../../helpers/LinkDragManager';
import { renderBaselines } from './baselines';
import { gridConstants, getShadowRootHost } from '@devsnc/sn-datagrid';
import { getBarColors } from '../../../templates/timeline';
const {
	CELL_CLICKED,
	CELL_BLURRED,
	ARROW_DOWN_KEY,
	ARROW_UP_KEY,
	KEYBOARD_NAVIGATED,
	PRIMARY
} = gridConstants;

const { MILESTONE } = TASK_TYPES;

const getMilestoneColors = ({
	barStyles
}) => {
	const milestoneStyle = {};
	const milestoneColor = get(barStyles, 'milestone.color');

	if (!isEmpty(milestoneColor)) {
		milestoneStyle.backgroundColor = milestoneColor;
	}

	return milestoneStyle;
};

const isRowSelected = (selectedCell, rowIndex) => {
	return selectedCell && selectedCell.rowIndex === rowIndex + 1;
};

const getLinkAwayFromTask = () => {
	return DEPDENCY_CIRCLE_AND_TASK_RESIZER_WIDTH / 2 +
	GAP_BETWEEN_DEPENDENCY_CIRCLE_AND_TASK_RESIZER;
};

const getTaskActionsContainerWidth = (showLinkTerminal, showResizeIcons) => {
	if (showLinkTerminal && showResizeIcons) {
		return TASK_ACTIONS_CONTAINER_WIDTH;
	} else if (!showLinkTerminal) {
		return TASK_ACTIONS_CONTAINER_WIDTH -
				DEPDENCY_CIRCLE_AND_TASK_RESIZER_WIDTH -
				GAP_BETWEEN_DEPENDENCY_CIRCLE_AND_TASK_RESIZER;
	} else {
		return TASK_ACTIONS_CONTAINER_WIDTH - DEPDENCY_CIRCLE_AND_TASK_RESIZER_WIDTH;
	}
};

const getAllowedDependencyTerminals = (canCreateDependency, allowedDependencyTypes = []) => {
	const start = {
		displaySourceTerminal: false,
		displayTargetTerminal: {
			[START]: false,
			[FINISH]: false
		}
	};
	const finish = {
		displaySourceTerminal: false,
		displayTargetTerminal: {
			[START]: false,
			[FINISH]: false
		}
	};

	for (let i = 0; i < allowedDependencyTypes.length; i++) {
		const type = allowedDependencyTypes[i];
		if (type === LINKS.FINISH_TO_START) {
			finish.displaySourceTerminal = canCreateDependency;
			start.displayTargetTerminal[FINISH] = canCreateDependency;
		} else if (type === LINKS.START_TO_FINISH) {
			start.displaySourceTerminal = canCreateDependency;
			finish.displayTargetTerminal[START] = canCreateDependency;
		} else if (type === LINKS.START_TO_START) {
			start.displaySourceTerminal = canCreateDependency;
			start.displayTargetTerminal[START] = canCreateDependency;
		} else if (type === LINKS.FINISH_TO_FINISH) {
			finish.displaySourceTerminal = canCreateDependency;
			finish.displayTargetTerminal[FINISH] = canCreateDependency;
		}
	}

	return { START: start, FINISH: finish };
};
const shouldDisplayPoint = (
	isCreatingDependency, isCurrentTaskActive, drawingLinkData, currentTaskId, position, allowedPositions
) => {
	const { displaySourceTerminal, displayTargetTerminal } = allowedPositions[position];

	if (isCreatingDependency) {
		const { source, sourceTerminal } = drawingLinkData;
		if (currentTaskId === source) {
			return sourceTerminal === position;
		} else {
			return isCurrentTaskActive && displayTargetTerminal[sourceTerminal];
		}
	} else {
		return isCurrentTaskActive && displaySourceTerminal;
	}
};

/**
 * Event handlers for timeline bars. i.e. click, contextmenu.
 * @param {String} eventName Event that needs to be dispatched
 * @param {Object} taskData Task Data as payload
 * @param {Object} dispatch NOW UI Dispatch API
 */
const barEventHandler = (eventName, taskData, dispatch) => {
	return (event) => {
		event.preventDefault();
		const { x, y } = event;

		dispatch(eventName, { x, y, ...taskData });
	};
};

const renderBars = (state, dispatch) => {
	const {
		properties,
		movingTaskInfo,
		activeTaskId,
		drawingLinkData,
		textTypeColumnName
	} = state;
	const {
		dir,
		config,
		visibleTasks,
		bodyWrapperRef,
		baselineStatus,
		selectedCell,
		columns,
		links,
		positionMaps: {
			taskPositionMap,
			taskLayerPositionMap,
			baselinePositionMap
		}
	} = properties;
	const {
		barStyles,
		rowHeight,
		showBarLabel,
		taskHeight,
		timelinePopoverConfig = {},
		canMoveTask,
		canResizeTask,
		canCreateDependency,
		canShowBaselines,
		showBaselines,
		taskLayers,
		barTemplate,
		allowedDependencyTypes
	} = config;
	const { displayPopover = true } = timelinePopoverConfig;
	const isRTL = dir === RTL;
	const transformXSign = isRTL ? '-' : '';
	const enableBaselines = canShowBaselines && showBaselines;
	const template = barTemplate && typeof barTemplate === 'function' ? barTemplate : templates.barTemplate;
	const margin = (rowHeight - taskHeight) / 2;
	const allowedPositions = getAllowedDependencyTerminals(canCreateDependency, allowedDependencyTypes);
	const milestoneStyle = getMilestoneColors({ barStyles });
	const isCreatingDependency = drawingLinkData.source !== null && drawingLinkData.source !== undefined;
	const handleKeydown = e => {
		switch (e.keyCode) {
			case ARROW_UP_KEY:
			case ARROW_DOWN_KEY:
				e.preventDefault();
				dispatch(KEYBOARD_NAVIGATED, {
					keyCode: e.keyCode,
					isRTL,
					segment: PRIMARY,
					bodyWrapperRef
				});
				break;
			case TAB_KEY:
				dispatch(CELL_BLURRED);
		}
	};

	return (
		<Fragment>
			{taskPositionMap ? visibleTasks.map(task => {
				const { childCount, taskData, index, id, temporary } = task;
				const taskRect = taskPositionMap[id];
				if (temporary || !taskRect) {
					return <Fragment />;
				}
				const top = index * rowHeight;
				const isBarSelected = isRowSelected(selectedCell, index) && selectedCell.columnId === TIMELINE_SECTION;
				const tabIndex = index === 0 || isBarSelected ? '0' : '-1';
				const isParent = childCount > 0;
				const isMilestone = taskData.type === MILESTONE;
				const isTaskActive = activeTaskId === id;
				const enableTaskDrag = !isCreatingDependency && (canMoveTask && !isParent);
				const showResizeIcons = canResizeTask && !isParent && !isMilestone && !isCreatingDependency && (
					isTaskActive || movingTaskInfo && movingTaskInfo.id === id
				);
				const containerClassNames = `sn-gantt-task-wrapper-container${!enableTaskDrag ? ' sn-gantt-task-dnd-disabled': ''}`;

				const showDependencyStartPoint = shouldDisplayPoint(
					isCreatingDependency, isTaskActive, drawingLinkData, id, START, allowedPositions
				);
				const showDependencyEndPoint = shouldDisplayPoint(
					isCreatingDependency, isTaskActive, drawingLinkData, id, FINISH, allowedPositions
				);
				const taskActionsStartWidth = getTaskActionsContainerWidth(showDependencyStartPoint, showResizeIcons);
				const taskActionsEndWidth = getTaskActionsContainerWidth(showDependencyEndPoint, showResizeIcons);
				const barWidth = `${taskData.type === MILESTONE ? taskHeight : taskRect.width}px`;
				const handleBarFocus = () => {
					// If first bar is focussed through tab key and not arrow keys,
					// then bar is not selected according to the state
					// So dispatching an event to update the bar selection on state
					if (!isBarSelected) {
						dispatch(CELL_CLICKED, {
							rowIndex: index + 1,
							columnId: TIMELINE_SECTION,
							segment: PRIMARY
						});
					}
				};
				const [barColor] = getBarColors({ barStyles, $isParent: isParent });

				return (
					<div
						className='sn-gantt-timeline-body-row'
						style={{
							transform: `translate(0px, ${top}px)`,
							height: `${rowHeight}px`,
							pointerEvents: isCreatingDependency ? 'all' : 'none'
						}}
						on-mouseenter={() => {
							!movingTaskInfo && dispatch(TASK_MOUSE_ENTERED, { id });
						}}
						on-mouseleave={ () => {
							dispatch(TASK_MOUSE_LEFT);
						}}
					>
						<div
							style={{
								transform: `translateX(${transformXSign}${taskRect.left}px)`,
								marginTop: `${margin}px`,
								width: barWidth,
								minWidth: barWidth,
								pointerEvents: `${movingTaskInfo ? 'none': 'all'}`,
								height: `${taskHeight}px`
							}}
							className={containerClassNames}
							on-mouseenter={ displayPopover && !movingTaskInfo
								? e => {
									e.stopPropagation();
									renderPopover({
										host: getShadowRootHost(e.currentTarget),
										mousePos: e.clientX,
										task,
										taskRect,
										columns,
										timelinePopoverConfig,
										dispatch,
										isRTL
									});
								}
								: noop
							}
							on-mousedown={ enableTaskDrag
								? event => {
									dispatch(HIDE_BAR_POPOVER);
									if (event.which === 1 || event.button === 0) { // left mouse button
										onDragStart({
											event,
											dispatch,
											id: id,
											taskRect,
											isRTL,
											links
										});
									}
								} : noop
							}
							on-mouseleave={ displayPopover
								? e => {
									e.stopPropagation();
									destroyPopover({
										dispatch
									});
								}
								: noop }
							on-click= { barEventHandler(TASK_BAR_CLICKED_INTERNAL, taskData, dispatch) }
							on-contextmenu={ barEventHandler(TASK_BAR_RIGHT_CLICKED_INTERNAL, taskData, dispatch) }
						>
							{
								showDependencyStartPoint || showResizeIcons
									? <div className="task-actions-container" style={{
										height: `${taskHeight}px`,
										width: `${taskActionsStartWidth}px`,
										[isRTL ? 'right' : 'left']: `-${taskActionsStartWidth}px`
									}}>
										{
											showDependencyStartPoint ? <div
												className="sn-gantt-task-link-terminal terminal-left"
												style={{
													[isRTL ? 'marginLeft' : 'marginRight']: `${GAP_BETWEEN_DEPENDENCY_CIRCLE_AND_TASK_RESIZER}px`
												}}
												on-mousedown={event => {
													dispatch(HIDE_BAR_POPOVER);
													onLinkDragStart({
														event,
														dispatch,
														taskRect,
														yPos: top + rowHeight / 2,
														id,
														awayFromTask: getLinkAwayFromTask(),
														isRTL
													});
												}}
												on-mouseup={event => onLinkDragMouseUp({
													event,
													dispatch,
													id
												})}
											>
											</div> : <Fragment />
										}
										{
											showResizeIcons ? <div
												className="task-resizer"
												style={barColor}
												on-mousedown={event => {
													event.preventDefault();
													event.stopPropagation();

													dispatch(HIDE_BAR_POPOVER);
													onResizeStart({
														event,
														id,
														dispatch,
														direction: RESIZE_START,
														isRTL,
														taskRect,
														links
													});
												}}
											>
												<now-icon icon="grid-vertical-fill"/>
											</div> : <Fragment />
										}
									</div>
									: <Fragment />
							}
							{isMilestone
								? <div
									id={'time_line_bar_wrapper' + id}
									className="sn-gantt-task-wrapper sn-gantt-task-milestone-wrapper"
									tabIndex="-1"
								>
									<div
										tabIndex={tabIndex}
										id={'time_line_bar_' + id}
										className={`sn-gantt-task sn-gantt-task-milestone${isBarSelected ? ' ' + TIMELINE_BODY_ITEM_SELECTED : ''}`}
										style={milestoneStyle}
										on-focus={handleBarFocus}
										on-blur={() => destroyPopover({ dispatch })}
										on-keydown={handleKeydown}
									></div>
								</div>
								:<div
									tabIndex={tabIndex}
									id={'time_line_bar_' + id}
									className={`sn-gantt-task-wrapper${isBarSelected ? ' ' + TIMELINE_BODY_ITEM_SELECTED : ''}`}
									on-focus={handleBarFocus}
									on-blur={() => destroyPopover({ dispatch })}
									on-keydown={handleKeydown}
									style={{
										height: `${taskHeight}px`
									}}
								>
									{
										createElement(template, {
											$node: task,
											$config: config,
											$index: index,
											$isParent: isParent,
											$rect: taskRect
										})
									}
								</div>
							}
							{
								showDependencyEndPoint || showResizeIcons
									? <div className="task-actions-container" style={{
										height: `${taskHeight}px`,
										width: `${taskActionsEndWidth}px`,
										[dir !== RTL ? 'right' : 'left']: `-${taskActionsEndWidth}px`
									}}>
										{
											showResizeIcons ? <div
												className="task-resizer"
												style={barColor}
												on-mousedown={event => {
													event.preventDefault();
													event.stopPropagation();

													dispatch(HIDE_BAR_POPOVER);
													onResizeStart({
														event,
														id,
														dispatch,
														direction: RESIZE_END,
														isRTL,
														taskRect,
														links
													});
												}}
											>
												<now-icon icon="grid-vertical-fill"/>
											</div> : <Fragment />
										}
										{
											showDependencyEndPoint ? <div
												className="sn-gantt-task-link-terminal terminal-right"
												style={{
													[isRTL ? 'marginRight' : 'marginLeft']: `${GAP_BETWEEN_DEPENDENCY_CIRCLE_AND_TASK_RESIZER}px`
												}}
												on-mousedown={event => {
													dispatch(HIDE_BAR_POPOVER);
													onLinkDragStart({
														event,
														dispatch,
														taskRect,
														yPos: top + rowHeight / 2,
														id,
														awayFromTask: getLinkAwayFromTask(),
														isRTL
													});
												}}
												on-mouseup={event => onLinkDragMouseUp({
													event,
													dispatch,
													id
												})}
											>
											</div> : <Fragment />
										}
									</div>
									: <Fragment />
							}
							{showBarLabel
								? <label
									className="sn-gantt-task-label text-ellipsis"
									style={{
										transform: `
										translateY(calc(-${taskHeight/2}px - 0.75em))
										`
									}}
								>
									{
										get(taskData, `${textTypeColumnName}.displayValue`, '')
									}
								</label>
								: <Fragment />}
						</div>
						{taskLayerPositionMap[id]
							? <div className="sn-gantt-timeline-body-row-task-layers">
								{
									taskLayers.map((layer) => {
										const positions = taskLayerPositionMap[id][layer.columnName];

										return positions
											? positions.map(position => {
												return createElement(layer.renderer, {
													task: task.taskData,
													position
												});
											})
											: <Fragment/>;
									})
								}
							</div>
							: <Fragment />
						}
						{
							enableBaselines && baselinePositionMap[id] ? renderBaselines({
								top: margin + taskHeight,
								positions: baselinePositionMap[id],
								baselineStatus,
								id,
								transformXSign
							}) : <Fragment />
						}
					</div>
				);
			}) : <Fragment />}
		</Fragment>
	);
};

export default renderBars;
