/* eslint-disable max-len */
import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import rtlBehavior from '@servicenow/behavior-rtl';
import view from './view';
import componentActionsHandler from './actions/componentActions';
import { timelineActionHandlers } from './actions/timelineActions';
import { designSystemComponentActionHandlers } from './actions/designSystemComponentActions';
import {
	GRID_RESIZED,
	TIMELINE_ZOOM_CHANGED,
	LINK_CREATED,
	DEFAULT,
	GANTT_COLUMNS_UPDATED,
	GANTT_SORT_CHANGED,
	GANTT_ROW_EDIT_STARTED,
	GANTT_PREVIEW_RECORD_ICON_CLICKED,
	GANTT_ALL_ROW_SELECTED,
	GANTT_ALL_ROW_UNSELECTED,
	GANTT_FILTER_CHANGED,
	GANTT_ROW_SELECTED_SET,
	TASK_MOVED,
	TASK_RESIZED,
	LINK_CLICKED,
	LINK_MOUSEOUT,
	LINK_MOUSEOVER,
	TASK_BAR_RIGHT_CLICKED,
	TASK_BAR_CLICKED,
	DEFAULT_CONFIG,
	LINK_RIGHT_CLICKED,
	GANTT_DISPLAY_DEPENDENCY_LINES,
	GANTT_DISPLAY_BAR_LABELS,
	GANTT_ROW_EXPAND_SET,
	GANTT_ALL_ROWS_EXPANDED,
	GANTT_ALL_ROWS_COLLAPSED,
	GANTT_COLUMNS_RESET,
	GANTT_TASKS_PASTED,
	GANTT_ROW_ORDER_CHANGED,
	GANTT_INSERT_COPIED_ROWS
} from './constants';
import styles from './now-gantt.scss';
import {
	dragAndDropActionHandlers,
	columnDragAndDropActionHandlers,
	gridActionHandlers,
	gridClientActionHandlers,
	keyboardNavHandlers,
	panelActionHandlers,
	getGridInitialState,
	eventHandlers as gridEventHandlers,
	transformState
} from '@devsnc/sn-datagrid';
import { ganttGridActionHandlers } from './actions/grid';
import { getMergedActionHandlers } from './helpers/mergeActionHandlers';
import configSchema from './schemas/config';
import columnSchema from './schemas/column';
import linkSchema from './schemas/link';

/**
 * Gantt is an UI component that  accept data and  configuration to render Gantt chart.
 * The primary features of Gantt component is to display Hierarchical tasks in the grid,
 * their timeline as horizontal bar and dependencies among tasks.
 *
 * ```jsx
 * 	<now-gantt
 *		startDate="2019-07-11 02:30:00"
 *		endDate="2019-07-15 11:30:00"
 *		columns={[{name:  'text', label:  'Name', width:  300}, {name:  'startDate', label:  'Start Date'}, {name:  'endDate', label:  'End Date'}]}
 *		config={{readonly:  true, headerHeight:  50, gridWidth:  45}}
 *		tasks={[{id:  1, text:  'Project #1', startDate: '2019-07-11 02:30:00', endDate: '2019-07-15 11:30:00'}, {id:  2, text:  'Task #1', parent:  1, startDate: '2019-07-11 02:30:00', endDate: '2019-07-15 11:30:00'}, {id:  3, text:  'Task #2', parent:  2, startDate: '2019-07-11 02:30:00', endDate: '2019-07-15 11:30:00'}]}
 *		links={[{id:  1, source:  2, target:  3, type:  0}]}>
 *	</now-gantt>
 * ```
 *
 * @seismicElement now-gantt
 * @summary A component to visualize Gantt charts.
 */

createCustomElement('now-gantt', {
	renderer: {
		type: snabbdom
	},
	view,
	initialState: {
		...getGridInitialState(),
		config: DEFAULT_CONFIG,
		timelineData: null,
		visibleHeight: 0,
		visibleWidth: 0,
		possibleZoomLevels: [],
		isLoading: false,
		positionMaps: {
			taskPositionMap: {},
			linkPositionMap: {},
			taskLayerPositionMap: {},
			baselinePositionMap: {}
		},
		layout: DEFAULT,
		baselineDisplayStatus: [true, true],
		scrollTop: 0,
		startDate: new Date(),
		endDate: new Date(),
		hideTimeline: false
	},
	transformState,
	properties: {
		/**
		 * General configuration for gantt. Includes
		 * Width of the grid
		 * Zoom level configurations
		 * Hierarchy mode or Group mode
		 * Timeline popover configurations
		 * @type {{}}
		 */
		config: {
			default: {},
			schema: configSchema
		},
		/**
		 * Start date for the gantt timeline
		 * @type {string}
		 */
		startDate: {
			schema: {
				type: 'string'
			}
		},
		/**
		 * End date for the gantt timeline
		 * @type {string}
		 */
		endDate: {
			schema: {
				type: 'string'
			}
		},
		/**
		 * An array of column objects that describe each column header in the grid.
		 * @type {Object[]}
		 */
		columns: {
			default: [],
			schema: {
				type: 'array',
				items: columnSchema
			}
		},
		/**
		 * An object to sort the column with the order
		 * @type {{name: string, order: 'asc'|'desc'}}
		 */
		sortBy: {
			default: {},
			schema: {
				type: 'object',
				properties: {
					name: { type: 'string' },
					order: { type: 'string', enum: ['asc', 'desc'] }
				}
			}
		},
		/**
		 * An array of task objects that displays rows in the grid.
		 * @type {Object[]}
		 */
		tasks: {
			default: [],
			schema: {
				type: 'array',
				items: {
					type: 'object'
				}
			}
		},
		/**
		 * Contains the timeline dependencies that neede to be drawn on timeline
		 * @type {Object[]}
		 */
		links: {
			default: [],
			schema: {
				type: 'array',
				items: linkSchema
			}
		},
		/**
		 * A boolean flag to hide the gantt timeline
		 * @type {Boolean}
		 */
		hideTimeline: {
			default: false,
			schema: {
				type: 'boolean'
			}
		},
		/**
		 * Contains the list of holidays for blocking the timeline
		 * @type {string[]}
		 */
		holidays: {
			default: [],
			schema: {
				type: 'array'
			}
		}
	},
	styles: styles,
	actionHandlers: {
		...getMergedActionHandlers(componentActionsHandler, keyboardNavHandlers, gridActionHandlers),
		...gridClientActionHandlers,
		...timelineActionHandlers,
		...dragAndDropActionHandlers,
		...columnDragAndDropActionHandlers,
		...panelActionHandlers,
		...designSystemComponentActionHandlers,
		...ganttGridActionHandlers
	},
	behaviors: [{ behavior: rtlBehavior }],
	dispatches: {
		/**
		 * Dispatched when the grid is resized
		 * @type {{ width: number }}
		 */
		[GRID_RESIZED]: {},
		/**
		 * Dispatched when timeline zoom level is changed
		 * @type {{ id: string, }}
		 */
		[TIMELINE_ZOOM_CHANGED]: {},
		/**
		 * Dispatched when task moved from one location to another
		 * @type {{ id: string, startDate: string, endDate: string }}
		 */
		[TASK_MOVED]: {},
		/**
		 * Dispatched when task bar is resized
		 * @type {{ id: string, startDate: string, endDate: string }}
		 */
		[TASK_RESIZED]: {},
		/**
		 * Dispatched when dependency created
		 * @type {{ id: number, source: string, target: string, type: string }}
		 */
		[LINK_CREATED]: {},
		/**
		 * Dispatched when dependency clicked
		 * @type {{ id: number, x: number, y: number, ...link}}
		 */
		[LINK_CLICKED]: {},
		/**
		 * Dispatched when there is mouseover on dependency
		 * @type {{ id: number, x: number, y: number, ...link}}
		 */
		[LINK_MOUSEOVER]: {},
		/**
		 * Dispatched when there is mouseover on dependency
		 * @type {{ id: number, x: number, y: number, ...link}}
		 */
		[LINK_MOUSEOUT]: {},
		/**
		 * Dispatched when there is right click on dependency
		 * @type {{ id: number, x: number, y: number, ...link}}
		 */
		[LINK_RIGHT_CLICKED]: {},
		/**
		 * Dispatched when task bar is clicked
		 * @type {{ id: number }}
		 */
		[TASK_BAR_CLICKED]: {},
		/**
		 * Dispatched when task bar is right clicked
		 * @type {{ id: number }}
		 */
		[TASK_BAR_RIGHT_CLICKED]: {},
		/**
		 * Dispatched when the one of the properties width,
		 * hidden, section, order of one or more than one column is changed.
		 * Payload contains an array of columns.
		 * @type {columns: Object[]}
		 */
		[GANTT_COLUMNS_UPDATED]: {},
		/**
		 * Dispatched when sort order or sorting column changes
		 * Payload contains the sortBy object with name and order property.
		 * @type {{sortBy: Object}}
		 */
		[GANTT_SORT_CHANGED]: {},

		/**
		 * Dispatched when column filter changes
		 * @type {{filterExpression: String}}
		 */
		[GANTT_FILTER_CHANGED]: {},

		/**
		 * Dispatched when editing is enabled with rowEdit editMode and cell is double-clicked
		 * @type {{taskId: string, columnsConfig: Object[]}}
		 */
		[GANTT_ROW_EDIT_STARTED]: {},
		/**
		 * Dispatched when Preview icon is clicked.
		 * @type {{taskId: String}}
		 */
		[GANTT_PREVIEW_RECORD_ICON_CLICKED]: {},

		/**
		 * Dispatched when a row is selected or unselected
		 * @type {{taskId: String}}
		 */
		[GANTT_ROW_SELECTED_SET]: {},

		/**
		 * Dispatched when all rows are selected
		 * @type {undefined}
		 */
		[GANTT_ALL_ROW_SELECTED]: {},

		/**
		 * Dispatched when all rows are unselected
		 * @type {undefined}
		 */
		[GANTT_ALL_ROW_UNSELECTED]: {},

		/**
		 * Dispatched  when display bar label setting is toggled
		 * @type {{value: Boolean}}
		 */
		[GANTT_DISPLAY_BAR_LABELS]: {},

		/**
		 * Dispatched when display dependency lines setting is toggled
		 * @type {{value: Boolean}}
		 */
		[GANTT_DISPLAY_DEPENDENCY_LINES]: {},
		/**
		 * Dispatched when a row is expanded or collapsed
		 * @type {{taskId: String, value: Boolean}}
		 */
		[GANTT_ROW_EXPAND_SET]: {},

		/**
		 *  Dispatched when expand all button is clicked
		 * @type {undefined}
		 */
		[GANTT_ALL_ROWS_EXPANDED]: {},

		/**
		 *  Dispatched when collapse all button is clicked
		 * @type {undefined}
		 */
		[GANTT_ALL_ROWS_COLLAPSED]: {},
		/**
		 * Dispatched when reset columns button is clicked
		 * @type {undefined}
		 */
		[GANTT_COLUMNS_RESET]: {},

		/**
		 * Dispatched after pasting copied values.
		 * @type {{}}
		 */
		[GANTT_TASKS_PASTED]: {},

		/**
		 * Dispatched after reordering rows.
		 * @type {{}}
		 */
		[GANTT_ROW_ORDER_CHANGED]: {},
		/**
		 * Dispatched on clicking `Insert copied rows` after copying rows.
		 * The `targetTask` attribute contains the details of the task on which this action was dispatched.
		 * the `copiedTasks` attribute is an array of copied task objects
		 * @type {{}}
		 */
		[GANTT_INSERT_COPIED_ROWS]: {}
	},
	eventHandlers: [
		...gridEventHandlers
	],
	slots: {
		/**
		 * 'toolbar-icons' slot can be used to add icons in the toolbar
		 * @toolbar-icons
		 */
		'toolbar-icons': {},
		/**
		 * 'empty-state' slot can be used to customize the empty state (No tasks) view
		 */
		'empty-state': {},
		/**
		 * 'settings-panel' slot can be used to add elememts in the settings panel
		 */
		'settings-panel': {}
	}
});
