import { Fragment } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line
export const renderBaselines = ({
	top,
	positions,
	baselineStatus,
	transformXSign
}) => {
	return (
		<div
			className="baseline-container"
			style={{
				top: `${top}px`
			}}
		>
			{
				positions.map((pos, index) => {
					return baselineStatus[index]
						? <div
							className="baseline"
							style={{
								transform: `translateX(${transformXSign}${pos.left}px)`,
								width: `${pos.width}px`
							}}
							data-index={String(index)}
						></div> : <Fragment />;
				})
			}
		</div>
	);
};
