/*
	With current grid/gant integration where action handlers for both have to be combined and used in now-gantt
	We have to combine action handlers common to both, eg: Design system actions, component rendered etc
	This utility combines action handlers. If any action handler has attributes of modifier, sropPropation
	The first such action handler's attribute is applied to the combined action handler
*/
export const getMergedActionHandlers = (...actionHandlerSets) => {
	const combinedActionHandlers = {};

	actionHandlerSets.forEach(actionHandlerSet => {
		Object.keys(actionHandlerSet).forEach(actionHandlerName => {
			if (!combinedActionHandlers.hasOwnProperty(actionHandlerName)) {
				combinedActionHandlers[actionHandlerName] = {
					effect: []
				};
			}

			const combinedHandler = combinedActionHandlers[actionHandlerName];
			const actionHandler = actionHandlerSet[actionHandlerName];

			if (typeof actionHandler === 'function') {
				combinedHandler.effect.push(actionHandler);
			} else if (typeof actionHandler === 'object') {
				combinedHandler.effect.push(actionHandler.effect);
				if (!combinedHandler.hasOwnProperty('stopPropagation') && actionHandler.hasOwnProperty('stopPropagation')) {
					combinedHandler.stopPropagation = actionHandler.stopPropagation;
				}
				if (!combinedHandler.hasOwnProperty('modifier') && actionHandler.hasOwnProperty('modifier')) {
					combinedHandler.modifier = actionHandler.modifier;
				}
			}
		});
	});

	Object.keys(combinedActionHandlers).forEach(actionHandlerName => {
		const combinedHandler = combinedActionHandlers[actionHandlerName];

		if (combinedHandler.effect.length === 1) {
			combinedHandler.effect = combinedHandler.effect[0];
		} else {
			const actionHandlerArray = Object.assign([], combinedHandler.effect);
			combinedHandler.effect = (params) => {
				actionHandlerArray.forEach(actionHandler => actionHandler(params));
			};
		}
	});

	return combinedActionHandlers;
};
