// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import noop from 'lodash/noop';
import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './styles.scss';
import rtlBehavior from '@servicenow/behavior-rtl';
import renderTimelineHeader from './renderers';
import renderCalendar from './renderers/calendar';
import { timelineHeaderActions } from './actions';

createCustomElement('sn-gantt-timeline-header', {
	renderer: {
		type: snabbdom
	},
	view: (state, { dispatch }) => {
		const {
			timelineData,
			possibleZoomLevels
		} = state.properties;

		return <Fragment>
			{
				renderTimelineHeader(state, dispatch)
			}
			{
				renderCalendar({
					timelineData,
					possibleZoomLevels
				})
			}
		</Fragment>;
	},
	styles,
	actionHandlers: {
		...timelineHeaderActions
	},
	initialState: {
		topScaleData: {
			scales: [],
			translate: 0
		},
		bottomScaleData: {
			scales: [],
			translate: 0
		},
		scrollPos: 0,
		scaleCache: {}
	},
	properties: {
		dir: {
			default: null
		},
		config: {
			default: {}
		},
		timelineData: {
			default: {}
		},
		selectedCell: {
			default: {}
		},
		bodyWrapperRef: {
			default: null
		},
		setRef: {
			default: noop
		},
		syncScroll: {
			default: noop
		},
		possibleZoomLevels: {
			default: []
		},
		holidays: {
			default: []
		}
	},
	behaviors: [{ behavior: rtlBehavior }]
});
