import {
	TASK_DRAGGING_POSITION_UPDATED,
	EDGE_SIZE,
	MAX_STEP,
	TASK_MIN_POS,
	THROTTLE_TIME
} from '../constants';
import { getShadowRootHost } from '@devsnc/sn-datagrid';
import throttle from 'lodash/throttle';

/**
 * mouseOffsetX stores the distance from the left edge of task to the mouse postion
 * It is used to show the tooltip
 */
let mouseOffsetX = 0;
let initialX = 0;
let left = null;
let scrollContainer;
let scrollContainerRect;
let dragok = false;
let leftScrollInterval;
let rightScrollInterval;
let initialScrollLeft;
let links = [];

const getPosAndEdges = (pos, containerRect) => {
	const { left, width } = containerRect;

	return {
		posX: pos - left,
		edgeLeft: EDGE_SIZE,
		edgeRight: width - EDGE_SIZE,
		width
	};
};

const getValidIntensity = (intensity) => {
	return Math.max(Math.min(intensity, 1), -1);
};

export const canScrollStart = ({
	event,
	scrollContainer,
	scrollContainerRect,
	isRTL
}) => {
	const { posX, edgeLeft, edgeRight, width } = getPosAndEdges(event.clientX, scrollContainerRect);
	const isInLeftEdge = posX < edgeLeft;
	const isInRightEdge = posX > edgeRight;
	const isInStartEdge = isRTL ? isInRightEdge : isInLeftEdge;
	const hasScrollStart = isRTL
		? scrollContainer.scrollLeft + width < scrollContainer.scrollWidth
		: scrollContainer.scrollLeft > 0;

	return isInStartEdge && hasScrollStart;
};

export const canScrollTop = ({
	event,
	scrollContainer,
	scrollContainerRect
}) => {
	const viewportY = event.clientY - scrollContainerRect.top;
	const edgeLeft = EDGE_SIZE;
	const isInTopEdge = viewportY < edgeLeft;
	const hasScrollTop = scrollContainer.scrollTop !== 0;

	return isInTopEdge && hasScrollTop;
};

export const setScrollStart = ({
	event,
	scrollContainer,
	scrollContainerRect,
	isRTL
}) => {
	const { posX, edgeLeft, edgeRight } = getPosAndEdges(event.clientX, scrollContainerRect);
	const intensity = getValidIntensity((isRTL ? edgeRight - posX : edgeLeft - posX) / EDGE_SIZE);
	const newScrollX = Math.min(
		Math.max(scrollContainer.scrollLeft - MAX_STEP * intensity, 0),
		scrollContainer.scrollWidth
	);

	scrollContainer.scrollTo(newScrollX, 0);
};

export const setScrollTop = ({
	event,
	scrollContainer,
	scrollContainerRect
}) => {
	const viewportY = event.clientY - scrollContainerRect.top;
	const edgeTop = EDGE_SIZE;
	const intensity = getValidIntensity((edgeTop - viewportY) / EDGE_SIZE);
	const diffY = MAX_STEP * intensity;
	const newScrollY = scrollContainer.scrollTop - diffY;

	scrollContainer.scrollTo(0, newScrollY);

	return diffY;
};

export const canScrollEnd = ({
	event,
	scrollContainer,
	scrollContainerRect,
	isRTL
}) => {
	const { posX, edgeLeft, edgeRight, width } = getPosAndEdges(event.clientX, scrollContainerRect);
	const isInLeftEdge = posX < edgeLeft;
	const isInRightEdge = posX > edgeRight;
	const isInEndEdge = isRTL ? isInLeftEdge : isInRightEdge;
	const hasScrollEnd = isRTL
		?	scrollContainer.scrollLeft > 0
		:	scrollContainer.scrollLeft + width < scrollContainer.scrollWidth;

	return isInEndEdge && hasScrollEnd;
};

export const canScrollBottom = ({
	event,
	scrollContainer,
	scrollContainerRect
}) => {
	const viewportY = event.clientY - scrollContainerRect.top;
	const viewportHeight = scrollContainerRect.height;
	const edgeBottom = viewportHeight - EDGE_SIZE;
	const isInBottomEdge = viewportY > edgeBottom;
	const hasScrollBottom =
		scrollContainer.scrollTop + scrollContainerRect.height < scrollContainer.scrollHeight;

	return isInBottomEdge && hasScrollBottom;
};

export const setScrollEnd = ({
	event,
	scrollContainer,
	scrollContainerRect,
	isRTL
}) => {
	const { posX, edgeLeft, edgeRight } = getPosAndEdges(event.clientX, scrollContainerRect);
	const intensity = getValidIntensity((isRTL ? posX - edgeLeft : posX - edgeRight) / EDGE_SIZE);
	const newScrollX = Math.min(
		Math.max(scrollContainer.scrollLeft + MAX_STEP * intensity, 0),
		scrollContainer.scrollWidth
	);

	scrollContainer.scrollTo(newScrollX, 0);
};

export const setScrollBottom = ({
	event,
	scrollContainer,
	scrollContainerRect
}) => {
	const viewportY = event.clientY - scrollContainerRect.top;
	const viewportHeight = scrollContainerRect.height;
	const edgeBottom = viewportHeight - EDGE_SIZE;
	const intensity = getValidIntensity((viewportY - edgeBottom) / EDGE_SIZE);
	const diffY = MAX_STEP * intensity;
	const newScrollY = scrollContainer.scrollTop + diffY;

	scrollContainer.scrollTo(0, newScrollY);

	return diffY;
};

export const cantScrollFurther = ({ scrollLeft, scrollWidth }, { width }) => {
	return scrollLeft <= 0 || scrollLeft + width >= scrollWidth;
};

const updateTaskMovePosition = ({
	id,
	dispatch,
	isMoving = false,
	width,
	mouseOffsetX,
	scrollWidth
}) => {
	dragok && dispatch(TASK_DRAGGING_POSITION_UPDATED, {
		id,
		left: Math.min(Math.max(left, TASK_MIN_POS), scrollWidth - TASK_MIN_POS - width),
		isMoving: isMoving,
		width: width,
		mouseOffsetX,
		links
	});
};

const onDrag = ({
	event,
	id,
	dispatch,
	taskRect,
	isRTL
}) => {
	event.preventDefault();

	if (!scrollContainer || !scrollContainerRect) {
		return;
	}
	const prevScrollLeft = scrollContainer.scrollLeft;
	let scrollOffset = prevScrollLeft - initialScrollLeft;
	let mouseXDiff = event.clientX - initialX;
	if (isRTL) {
		scrollOffset *= -1;
		mouseXDiff *= -1;
	}
	const updatedTaskX = taskRect.left + mouseXDiff + scrollOffset;
	const scrollParams = {
		event,
		scrollContainer,
		scrollContainerRect,
		isRTL
	};
	const taskMovePositionParams = {
		id,
		dispatch,
		isMoving: true,
		width: taskRect.width,
		mouseOffsetX,
		scrollWidth: scrollContainer.scrollWidth
	};
	/**
	 * Clear existing intervals on mouse move
	 */

	clearInterval(leftScrollInterval);
	clearInterval(rightScrollInterval);

	if (updatedTaskX > 0) {
		left = updatedTaskX;
		updateTaskMovePosition(taskMovePositionParams);

		if (canScrollStart(scrollParams)) {
			leftScrollInterval = setInterval(() => {
				setScrollStart(scrollParams);

				const incrementVal = Math.abs(scrollContainer.scrollLeft - prevScrollLeft);

				left = updatedTaskX - incrementVal;
				updateTaskMovePosition(taskMovePositionParams);
				if (!dragok || cantScrollFurther(scrollContainer, scrollContainerRect)) {
					clearInterval(rightScrollInterval);
				}
			}, THROTTLE_TIME);
		} else if (canScrollEnd(scrollParams)) {
			rightScrollInterval = setInterval(() => {
				setScrollEnd(scrollParams);

				const incrementVal = Math.abs(scrollContainer.scrollLeft - prevScrollLeft);

				left = updatedTaskX + incrementVal;
				updateTaskMovePosition(taskMovePositionParams);
				if (!dragok || cantScrollFurther(scrollContainer, scrollContainerRect)) {
					clearInterval(rightScrollInterval);
				}
			}, THROTTLE_TIME);
		}
	}

	event.stopImmediatePropagation();
};

const throttledDrag = throttle(onDrag, THROTTLE_TIME);

const onDragStart = ({
	event,
	id,
	dispatch,
	taskRect,
	isRTL,
	links: allLinks = []
}) => {
	event.preventDefault();
	initialX = event.clientX;
	dragok = true;

	const timelineComponent = getShadowRootHost(event.currentTarget);
	const timelineBody = timelineComponent.shadowRoot.querySelector('.sn-gantt-timeline-body');
	const timelineBodyRect = timelineBody.getBoundingClientRect();

	scrollContainer = timelineComponent.shadowRoot.querySelector('.sn-gantt-timeline-body-wrapper');
	scrollContainerRect = scrollContainer.getBoundingClientRect();
	initialScrollLeft = scrollContainer.scrollLeft;


	if (isRTL) {
		mouseOffsetX = timelineBodyRect.right - event.clientX - taskRect.left;
	} else {
		mouseOffsetX = event.clientX - timelineBodyRect.left - taskRect.left;
	}
	// Before moving storing the links whose position has to be updated on task drag
	links = allLinks.filter(link => link.source === id || link.target === id);

	// Register mousemove event
	const handleMouseMove = (e) => {
		throttledDrag({
			event: e,
			dispatch,
			id,
			taskRect,
			isRTL
		});
	};

	// Register mouseup event on document
	const handleMouseUp = () => {
		// If mouse doesnt move then left is null, so not updating task position
		left && updateTaskMovePosition({
			id,
			dispatch,
			width: taskRect.width,
			mouseOffsetX,
			scrollWidth: scrollContainer.scrollWidth
		});

		// Reset event handlers and global variables
		dragok = false;
		left = null;
		initialX = null;
		scrollContainer = null;
		scrollContainerRect = null;
		initialScrollLeft = null;
		mouseOffsetX = 0;
		links = [];
		window.removeEventListener('mousemove', handleMouseMove);
		window.removeEventListener('mouseup', handleMouseUp);

		clearInterval(leftScrollInterval);
		clearInterval(rightScrollInterval);
	};

	window.addEventListener('mousemove', handleMouseMove);
	window.addEventListener('mouseup', handleMouseUp);
};

export default onDragStart;
