import {
	LINK_DRAG_STARTED,
	LINK_DRAG_ENDED,
	LINK_DRAG_MOVED,
	DEFAULT_DRAWING_LINK_DATA
} from '../constants';
import { LINK_ADDED } from '../../constants';

export const linkDragActionHandlers = {
	[LINK_DRAG_STARTED]: {
		stopPropagation: true,
		effect({
			action: {
				payload
			},
			updateState
		}) {
			updateState({
				drawingLinkData: {
					...payload
				}
			});
		}
	},
	[LINK_DRAG_MOVED]: {
		stopPropagation: true,
		effect({
			action: {
				payload
			},
			state: {
				drawingLinkData
			},
			updateState
		}) {
			updateState({
				drawingLinkData: {
					...drawingLinkData,
					...payload
				}
			});
		}
	},
	[LINK_DRAG_ENDED]: {
		stopPropagation: true,
		effect({
			action: {
				payload
			},
			updateState,
			dispatch
		}) {
			updateState({
				drawingLinkData: DEFAULT_DRAWING_LINK_DATA
			});

			// If dropped id is valid then create dependency
			if (payload.target) {
				dispatch(LINK_ADDED, {
					...payload
				});
			}
		}
	}
};
