
import {
	GRID_RESIZED, GRID_RESIZER_MAX, GRID_RESIZER_MIN
} from '../constants';

const getElementStart = (elementRef, dir) => {
	if (dir === 'ltr') {
		return elementRef.getBoundingClientRect().left;
	}

	return elementRef.getBoundingClientRect().right;
};

export const resizeGrid = (dispatch, dir, gridWidth, ganttContainerRef) => {
	const { current: ganttContainer } = ganttContainerRef;
	const ganttStart = getElementStart(ganttContainer, dir);
	const maxWidth = ganttContainer.clientWidth;
	let width = gridWidth;

	ganttContainer.style.pointerEvents = 'none';

	function resize(ev) {
		width = Math.abs((ev.pageX - ganttStart)/maxWidth*100);

		if (width <= GRID_RESIZER_MIN) {
			width = GRID_RESIZER_MIN;
		} else if (width >= GRID_RESIZER_MAX) {
			width = GRID_RESIZER_MAX;
		}
		dispatch(GRID_RESIZED, { width });
	}

	function stopResize() {
		dispatch(GRID_RESIZED, { width });
		window.removeEventListener('mouseup', stopResize);
		window.removeEventListener('mousemove', resize);
		ganttContainer.style.pointerEvents = 'all';
	}
	window.addEventListener('mousemove', resize);
	window.addEventListener('mouseup', stopResize);
};


