import renderRow from './row';

const renderGrid = (state) => {
	const { totalWidth } = state.properties.timelineData;

	return (
		<div
			className="sn-gantt-timeline-header-grid"
			style={{
				width: `${totalWidth}px`,
				minWidth: `${totalWidth}px`
			}}
		>
			{renderRow(state)}
		</div>
	);
};

export default renderGrid;
