import {
	getDateFromPostion, getEndDatefromStartDateOffset
} from '../helpers/TaskManager';
import { getTreeUtils } from '@devsnc/sn-datagrid';
import {
	TASK_DRAGGING_POSITION_UPDATED,
	DEFAULT_DRAWING_LINK_DATA,
	TASK_MOVED_INTERNAL,
	RESIZE_START,
	TIME_UNITS,
	RESIZE_END
} from '../constants';
import { TIMELINE_EXPAND_REQUESTED } from '../../constants';
import { getUpdatedPositionMapForTask } from '../helpers/TaskAndLinkPositionManager';
import DateHelper from '../helpers/DateHelper';
import get from 'lodash/get';
const { getNativeDiff } = DateHelper;

export const taskMoveActionHandlers = {
	[TASK_DRAGGING_POSITION_UPDATED]: {
		stopPropagation: true,
		effect({
			updateState,
			updateProperties,
			action: {
				payload: {
					id,
					left,
					width,
					isMoving,
					direction,
					mouseOffsetX,
					links = []
				}
			},
			state: {
				properties: {
					timelineData,
					tree,
					positionMaps
				},
				movingTaskInfo
			},
			dispatch
		}) {
			const {
				taskPositionMap,
				linkPositionMap
			} = positionMaps;

			/**
			 *    Based on left value calculate the start date and end date
			 *    Apply rounded value once the bar moving is stopped
			 */
			const { precision, zoomLevel } = timelineData;
			const { rangeUnit, width: unitWidth } = zoomLevel.config.bottom;
			const {
				startDate: { value: origStart },
				endDate: { value: origEnd }
			} = getTreeUtils(tree).getNodeById(id).taskData;
			let startDate,
				endDate;
			// StartDate calc
			if (direction === RESIZE_END) {
				startDate = origStart;
			} else {
				startDate = getDateFromPostion({ timelineData, position: left });
			}
			// EndDate calc
			if (direction === RESIZE_START) {
				endDate = origEnd;
			} else if (direction === RESIZE_END) {
				const position = precision === TIME_UNITS.DAY
					? left + width - unitWidth/2
					: left + width;

				endDate = getDateFromPostion({ timelineData, position });
			} else {
				// Task move
				endDate = getEndDatefromStartDateOffset(origStart, startDate, origEnd, precision);
			}

			// Valid dates if endDate is endOfDay
			if (precision === TIME_UNITS.DAY && getNativeDiff(startDate, endDate, rangeUnit) < 0) {
				if (direction === RESIZE_END) {
					endDate = startDate;
				} else {
					startDate = endDate;
				}
			}

			const shouldNotUpdatePositionMap = isMoving &&
				startDate === get(movingTaskInfo, 'startDate') &&
				endDate === get(movingTaskInfo, 'endDate');
			if (shouldNotUpdatePositionMap) {
				return;
			}

			const updatedTaskAndLinkPositionMap = getUpdatedPositionMapForTask({
				timelineData,
				tree,
				task: {
					id,
					startDate,
					endDate,
					links
				},
				taskPositionMap,
				linkPositionMap
			});

			/**
			 * 	Update the properties with new task position map
			 *  and link position map
			 */
			updateProperties({
				positionMaps: {
					...positionMaps,
					...updatedTaskAndLinkPositionMap
				}
			});

			if (isMoving) {
				// Update the moving task metadata info in the state
				updateState({
					movingTaskInfo: {
						id,
						startDate,
						endDate,
						direction,
						mouseOffsetX
					}
				});
			} else {
				// Delete the moving task metadata in the state
				updateState({
					movingTaskInfo: null,
					drawingLinkData: DEFAULT_DRAWING_LINK_DATA,
					activeTaskId: null
				});

				dispatch(TIMELINE_EXPAND_REQUESTED, {
					startDate,
					endDate
				});

				dispatch(TASK_MOVED_INTERNAL, {
					id,
					startDate,
					endDate,
					direction
				});
			}
		}
	}
};
