// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import {
	SELECTED_ROW_CLASS,
	SELECTED_ROW_CELL_CLASS
} from '../../../constants';

const isRowSelected = (selectedCell, rowIndex) => {
	return selectedCell && selectedCell.rowIndex === rowIndex + 1;
};

/** 
 * renderBarsBackground renders a empty behind the actual bars so that
 * it can be highlighted without overlapping the timeline links and bars
 */

const renderBarsBackground = (state) => {
	const { properties } = state;

	const {
		config,
		visibleTasks,
		selectedCell,

		positionMaps: {
			taskPositionMap
		}
	} = properties;
	const { rowHeight } = config;


	return (
		<Fragment>
			{ taskPositionMap
				? visibleTasks.map(task => {
					const { index, selected } = task;
					const selectedRowCellClassName = isRowSelected(selectedCell, index) ? ' ' + SELECTED_ROW_CELL_CLASS: '';
					const selectedRowClassName = selected ? ' ' + SELECTED_ROW_CLASS: '';
					const top = index * rowHeight;

					return (
						<div
							className={`sn-gantt-timeline-body-row${selectedRowClassName}${selectedRowCellClassName}`}
							style={{
								transform: `translate(0px, ${top}px)`,
								height: `${rowHeight}px`,
								pointerEvents: 'none'
							}}>

						</div>
					);
				})
				: <Fragment/>
			}
		</Fragment>
	);
};

export default renderBarsBackground;
