import { Fragment } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line
import get from 'lodash/get';
import { RTL } from '../../../constants';
const renderScale = (scales, translate, height, getData, isRTL) => {
	return <div
		className="sn-gantt-timeline-header-row"
		style={{
			transform: `translateX(${isRTL ? -translate: translate}px)`,
			height,
			lineHeight: height // To place cell content h-v-center
		}}
	>
		{scales.map((item, index) => {
			const { text, width, holiday } = getData(item, index);

			return (
				<div
					className={`sn-gantt-timeline-header-cell text-ellipsis${holiday ? ' sn-gantt-timeline-header-cell--holiday': ''}`}
					style={{
						width: `${width}px`,
						maxWidth: `${width}px`
					}}
					title={text}
				>
					{text}
				</div>
			);
		})}
	</div>;
};
const renderTopRow = (state, subScaleHeight, isRTL) => {
	const { translate, scales } = state.topScaleData;
	const getData = item => item;

	return renderScale(scales, translate, subScaleHeight, getData, isRTL);
};
const renderRow = (state) => {
	const {
		config: { headerHeight },
		timelineData,
		dir
	} = state.properties;
	const { translate, scales } = state.bottomScaleData;
	const { displayOneScale } = timelineData;
	const width = get(timelineData, 'zoomLevel.config.bottom.width', 50);
	const getData = (item) => ({ text: item.text, width, holiday: item.holiday });
	const subScaleHeight = `${displayOneScale ? headerHeight : headerHeight / 2}px`;
	const isRTL = dir === RTL;

	return (
		<Fragment>
			{
				displayOneScale
					? <Fragment />
					: renderTopRow(state, subScaleHeight, isRTL)
			}
			{
				renderScale(scales, translate, subScaleHeight, getData, isRTL)
			}
		</Fragment>
	);
};

export default renderRow;
