import {
	TOGGLE_CLICKED,
	LAYOUT,
	TIMELINE_ZOOM_CHANGED,
	CALENDAR,
	DROPDOWN_CLICKED,
	GRID,
	TIMELINE,
	NOW_BUTTON_ICONIC_CLICKED,
	DEPENDENCY_TOGGLE,
	BAR_LABEL_TOGGLE,
	DEFAULT_CONFIG,
	GRID_RESIZER_TIMELINEVIEW,
	GRID_RESIZER_GRIDVIEW,
	LAYOUT_CHANGED,
	NOW_TABS_ITEM_SELECTED,
	SIDE_PANEL_TAB,
	BASELINES_TOGGLE,
	TOGGLE_DISPLAY_BASELINES,
	DEFAULT_ZOOM_IN_LEVEL,
	GANTT_DISPLAY_DEPENDENCY_LINES,
	GANTT_DISPLAY_BAR_LABELS
} from '../constants';
import { getNextZoomPercentage } from '../timeline/helpers/ZoomPercentageManager';
import { getTimelineDataWithPositions } from '../timeline/helpers/TimelineManager';
import get from 'lodash/get';
import { DEFAULT_SCALES } from '../timeline/constants';

const handleZoomLevelChange = ({
	action,
	updateState,
	dispatch,
	state: {
		properties: {
			links,
			tasks
		},
		config,
		visibleWidth,
		possibleZoomLevels,
		timelineData,
		tree,
		startDate,
		endDate
	}
}) => {
	const { payload: { item: { id } } } = action;
	const tConfig = {
		...config,
		zoomLevel: id
	};

	const updatedTimelineData = getTimelineDataWithPositions({
		startDate,
		endDate,
		config: tConfig,
		visibleWidth,
		multiplicationFactor: config.zoomLevel === DEFAULT_SCALES.AUTOFIT
			? DEFAULT_ZOOM_IN_LEVEL
			: get(timelineData, 'zoomPercentage.multiplicationFactor'),
		possibleZoomLevels,
		tasks,
		links,
		tree
	});

	updateState({
		config: tConfig,
		...updatedTimelineData
	});
	dispatch(TIMELINE_ZOOM_CHANGED, id);
};

const handleLayoutChange = ({
	action,
	dispatch
}) => {
	const { payload: { item: { id }, ganttContainerRef } } = action;
	switch (id) {
		case GRID:
			dispatch(LAYOUT_CHANGED, {
				layout: id,
				width: GRID_RESIZER_GRIDVIEW,
				ganttContainerRef
			});
			break;
		case TIMELINE:
			dispatch(LAYOUT_CHANGED, {
				layout: id,
				width: GRID_RESIZER_TIMELINEVIEW,
				ganttContainerRef
			});
			break;
		default:
			dispatch(LAYOUT_CHANGED, {
				layout: id,
				width: DEFAULT_CONFIG.gridWidth,
				ganttContainerRef
			});
	}
};

const handleDependencyLinesToggle = ({ state, updateState, dispatch }) => {
	const value = !state.config.displayDependencyLines;

	updateState({
		path: 'config.displayDependencyLines',
		value,
		operation: 'set'
	});
	dispatch(GANTT_DISPLAY_DEPENDENCY_LINES, { value });
};

const handleBarLabelsToggle = ({ state, updateState, dispatch }) => {
	const {
		config,
		scrollHeight: previousScrollHeight
	} = state;
	const showBarLabel = !config.showBarLabel;
	const previousRowHeight = config.rowHeight;
	const rowHeight = showBarLabel ? previousRowHeight + 10 : previousRowHeight - 10;

	updateState({
		config: {
			...config,
			showBarLabel,
			rowHeight
		},
		scrollHeight: previousScrollHeight / previousRowHeight * rowHeight
	});
	dispatch(GANTT_DISPLAY_BAR_LABELS, { value: showBarLabel });
};

const handleZoomPercentageChange = ({
	action: {
		payload: {
			actionType
		}
	},
	state: {
		config,
		visibleWidth,
		timelineData,
		properties: {
			links,
			tasks
		},
		possibleZoomLevels,
		tree,
		startDate,
		endDate
	},
	updateState
}) => {
	const nextZoomPercentage = getNextZoomPercentage(timelineData.zoomPercentage.multiplicationFactor, actionType);

	const updatedTimelineData = getTimelineDataWithPositions({
		startDate,
		endDate,
		config,
		visibleWidth,
		multiplicationFactor: get(nextZoomPercentage, 'multiplicationFactor'),
		possibleZoomLevels,
		tasks,
		links,
		tree
	});

	updateState({
		...updatedTimelineData
	});
};

export const designSystemComponentActionHandlers = {
	[DROPDOWN_CLICKED]: {
		stopPropagation: true,
		effect(params) {
			const { action } = params;
			const { payload } = action;
			switch (payload && payload.type) {
				case CALENDAR:
					handleZoomLevelChange(params);
					break;
				case LAYOUT:
					handleLayoutChange(params);
					break;
			}
		}
	},
	[NOW_BUTTON_ICONIC_CLICKED]: params => {
		const { action } = params;
		const { payload } = action;
		switch (payload && payload.type) {
			case 'zoomLevel':
				action.stopPropagation();
				handleZoomPercentageChange(params);
				break;
		}
	},
	[TOGGLE_CLICKED]: {
		stopPropagation: true,
		effect(params) {
			const { action: { payload = {} }, updateState } = params;
			const { type, value, index } = payload;
			switch (type) {
				case BASELINES_TOGGLE:
					updateState({
						path: 'config.showBaselines',
						value,
						operation: 'set'
					});
					break;
				case TOGGLE_DISPLAY_BASELINES:
					updateState({
						path: `baselineDisplayStatus[${index}]`,
						value,
						operation: 'set'
					});
					break;
				case BAR_LABEL_TOGGLE:
					handleBarLabelsToggle(params);
					break;
				case DEPENDENCY_TOGGLE:
					handleDependencyLinesToggle(params);
					break;
			}
		}
	},
	[NOW_TABS_ITEM_SELECTED]: {
		stopPropagation: true,
		effect({ action, updateState }) {
			const { payload = {} } = action;
			const { type, value } = payload;
			switch (type) {
				case SIDE_PANEL_TAB:
					updateState({
						path: 'sidePanel.mode',
						value,
						operation: 'set'
					});
					break;
			}
		}
	},
	[BAR_LABEL_TOGGLE]: handleBarLabelsToggle
};
