import { ZOOM_IN_ZOOM_OUTS } from '../default/zoomLevels';

export const findZoomPercentageIndex = (factor) => {
	return ZOOM_IN_ZOOM_OUTS.findIndex(zoomPercentage => zoomPercentage.multiplicationFactor === factor);
};

export const findZoomPercentage = (factor) => {
	return ZOOM_IN_ZOOM_OUTS[
		findZoomPercentageIndex(factor)
	];
};

export const getNextZoomPercentage = (multiplicationFactor, actionType) => {
	const currentIndex = findZoomPercentageIndex(multiplicationFactor);
	let nextIndex = null;

	if (actionType === 'zoomIn') {
		nextIndex = currentIndex + 1;
	} else if (actionType === 'zoomOut') {
		nextIndex = currentIndex - 1;
	} else {
		nextIndex = currentIndex;
	}

	// Handle array out of bounds
	if (nextIndex < 0) {
		nextIndex = 0;
	} else if (nextIndex > ZOOM_IN_ZOOM_OUTS.length - 1) {
		nextIndex = ZOOM_IN_ZOOM_OUTS.length - 1;
	}

	return ZOOM_IN_ZOOM_OUTS[nextIndex];
};
