import renderGrid from './grid';
import { createRef } from '@servicenow/ui-renderer-snabbdom';
import { Fragment } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line
import { TIMELINE_SCROLL_REFS, HEADER, TIMELINE_SECTION, RTL } from '../../../constants';
import { gridConstants } from '@devsnc/sn-datagrid';
import { HEADER_HORIZONTAL_SCROLLED } from '../constants';
const { CELL_CLICKED, ARROW_DOWN_KEY, KEYBOARD_NAVIGATED, PRIMARY } = gridConstants;

const renderTimelineHeader = (state, dispatch) => {
	const {
		timelineData,
		setRef,
		syncScroll,
		dir,
		bodyWrapperRef,
		selectedCell = {}
	} = state.properties;
	const { rowIndex, columnId } = selectedCell;
	const timeLineHeaderRef = createRef();
	const isHeaderSelected = columnId === TIMELINE_SECTION && rowIndex === 0;

	setRef('timeLineHeaderRef', timeLineHeaderRef, TIMELINE_SCROLL_REFS);

	return (
		<div
			className="sn-gantt-timeline-header"
			id="sn-gantt-timeline-header"
			ref={timeLineHeaderRef}
			tabIndex="0"
			on-scroll={e => {
				dispatch(HEADER_HORIZONTAL_SCROLLED, { scrollLeft: e.target.scrollLeft });
				syncScroll(e, TIMELINE_SCROLL_REFS, HEADER);
			}
			}
			on-focus={() => {
				if (!isHeaderSelected) {
					dispatch(CELL_CLICKED, {
						rowIndex: 0,
						columnId: TIMELINE_SECTION,
						segment: PRIMARY
					});
				}
			}}
			on-keydown={e => {
				switch (e.keyCode) {
					case ARROW_DOWN_KEY:
						bodyWrapperRef && bodyWrapperRef.current.scrollTo({ top: 0 });
						e.preventDefault();
						dispatch(KEYBOARD_NAVIGATED, {
							keyCode: e.keyCode,
							isRTL: dir === RTL,
							segment: PRIMARY
						});
				}
			}}
		>
			{timelineData
				? <Fragment>
					{renderGrid(state)}
				</Fragment>
				: <Fragment />}
		</div>
	);
};

export default renderTimelineHeader;
