import { Fragment } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line
import { BAR_LABELS, DEPENDENCY_LINES_LABEL, LAYOUT_LABEL, GANTT_TAB_LABEL, SHOW_BASELINES, BASELINE_ONE, BASELINE_TWO } from '../../constants/translated';
import { LAYOUT, DEPENDENCY_TOGGLE, BAR_LABEL_TOGGLE, SIDE_PANEL_TAB, GANTT_TAB, LAYOUT_ITEMS, BASELINES_TOGGLE, TOGGLE_DISPLAY_BASELINES } from '../constants';
import {
	renderColumnConfigMenu,
	gridConstants
} from '@devsnc/sn-datagrid';
import '@servicenow/now-tabs';
const { COLUMNS, COLUMNS_LABEL } = gridConstants;

export const renderConfigPanelOptions = ({
	state,
	ganttContainerRef,
	dispatch
}) => {
	const {
		config,
		sidePanel,
		layout,
		baselineDisplayStatus,
		properties: { hideTimeline }
	} = state;
	const tabItems = [
		{ id: COLUMNS, label: COLUMNS_LABEL }
	];
	if (!hideTimeline || config.canSwitchLayout) {
		tabItems.push({ id: GANTT_TAB, label: GANTT_TAB_LABEL });
	}

	return (
		<div className="panel">
			<slot name="settings-panel"></slot>
			<now-tabs
				selectedItem={sidePanel.mode}
				items={tabItems}
				fixedWidth={true}
				appendToPayload={{ type: SIDE_PANEL_TAB }}
				className="side-panel-tabs"
			>
			</now-tabs>
			{
				sidePanel.mode === GANTT_TAB
					? <div className="timeline-config">
						{
							config.canShowBaselines ? <Fragment>
								<div className="panel-item">
									<p className="panel-item-label">{SHOW_BASELINES}</p>
									<now-toggle size="md" className="show-baselines-toggle" checked={config.showBaselines} append-to-payload={{ type: BASELINES_TOGGLE }}/>
								</div>
								{
									config.showBaselines ? <Fragment>
										<div className="panel-item">
											<p className="panel-item-label">{BASELINE_ONE}</p>
											<now-toggle size="md" className="baseline-one-toggle" checked={baselineDisplayStatus[0]} append-to-payload={{ type: TOGGLE_DISPLAY_BASELINES, index: 0 }}/>
										</div>
										<div className="panel-item">
											<p className="panel-item-label">{BASELINE_TWO}</p>
											<now-toggle size="md" className="baseline-two-toggle" checked={baselineDisplayStatus[1]} append-to-payload={{ type: TOGGLE_DISPLAY_BASELINES, index: 1 }}/>
										</div>
									</Fragment> : <Fragment />
								}
							</Fragment> : <Fragment />
						}
						<div className="panel-item">
							<p className="panel-item-label">{BAR_LABELS}</p>
							<now-toggle size="md" className="bar-labels-toggle" checked={config.showBarLabel} append-to-payload={{ type: BAR_LABEL_TOGGLE }}/>
						</div>
						<div className="panel-item">
							<p className="panel-item-label">{DEPENDENCY_LINES_LABEL}</p>
							<now-toggle size="md" className="dependency-lines-toggle" checked={config.displayDependencyLines} append-to-payload={{ type: DEPENDENCY_TOGGLE }}/>
						</div>
						{ config.canSwitchLayout
							? <div className="panel-item">
								<p className="panel-item-label">{LAYOUT_LABEL}</p>
								<now-dropdown
									append-to-payload={{
										type: LAYOUT,
										ganttContainerRef
									}}
									size="md"
									variant="secondary"
									select="single"
									selected-items={[layout]}
									items={LAYOUT_ITEMS}
									className="layout-dropdown"
								/>
							</div>
							: <Fragment />
						}
					</div>
					: renderColumnConfigMenu(state, dispatch)
			}
		</div>
	);
};
