import DateHelper from '../../helpers/DateHelper';
import { START, END } from '../../../../constants/translated';
import { RESIZE_END, ORDERED_TIME_UNITS, TOOLTIP_FORMAT_DATETIME, TOOLTIP_FORMAT_DATE, DRAG_TOOLTIP_TIME_WIDTH, WEEK_INDEX, TIME_UNITS } from '../../constants';
import { DRAG_TOOLTIP_WIDTH, DRAG_TOOLTIP_HEIGHT } from '../../constants';

const { getFormattedValue } = DateHelper;
const renderBarTooltip = ({
	startDate,
	endDate,
	direction,
	taskRect,
	isRTL,
	scrollTop,
	totalWidth,
	rowHeight,
	mouseOffsetX = 0,
	unit,
	top,
	precision,
	areDateTypeColumns
}) => {
	const {
		left,
		width
	} = taskRect;
	const end = direction === RESIZE_END;
	const bottom = top - scrollTop < DRAG_TOOLTIP_HEIGHT;
	const hasTimeComponent = precision !== TIME_UNITS.DAY && !areDateTypeColumns;
	const scaleUnitSupportsTime = ORDERED_TIME_UNITS.indexOf(unit) < WEEK_INDEX;
	const shouldDisplayTime = hasTimeComponent && scaleUnitSupportsTime;
	const tooltipFormat = shouldDisplayTime
		? TOOLTIP_FORMAT_DATETIME
		: TOOLTIP_FORMAT_DATE;
	const tooltipWidth = shouldDisplayTime ? DRAG_TOOLTIP_WIDTH + DRAG_TOOLTIP_TIME_WIDTH : DRAG_TOOLTIP_WIDTH;

	const leftPos = Math.min(
		Math.max(
			end
				? left + mouseOffsetX + width - tooltipWidth
				: left + mouseOffsetX,
			10
		),
		totalWidth - tooltipWidth - 10
	);

	const tooltipStyle = {
		width: `${tooltipWidth}px`,
		transform: `translate(${
			isRTL ? '-' : '+'}${leftPos
		}px, ${
			bottom ? top + rowHeight : top - DRAG_TOOLTIP_HEIGHT
		}px)`
	};

	return (
		<div className="tooltip"
			style={tooltipStyle}
		>
			<div className="tooltip-content">
				<div className="tooltip-row"><span>{START}:</span>{getFormattedValue(startDate, tooltipFormat)}</div>
			</div>
			<div className="tooltip-content">
				<div className="tooltip-row"><span>{END}:</span>{getFormattedValue(endDate, tooltipFormat)}</div>
			</div>
		</div>
	);
};

export default renderBarTooltip;
