
import { Fragment } from '@seismic/snabbdom-renderer'; //eslint-disable-line
import { createRef } from '@servicenow/ui-renderer-snabbdom';
import { getTreeUtils, gridConstants } from '@devsnc/sn-datagrid';
import './components/dependencies/sn-gantt-timeline-links';
import renderBarTooltip from './renderers/bar-tooltip';
import renderBarHoverTooltip from './renderers/bar-info-tooltip';
import drawDynamicLink from './renderers/drawingLink';
import renderBars from './renderers/bars';
import renderBarsBackground from './renderers/bars-background';
import renderVerticalLines from './renderers/verticalLines';
import { RTL } from '../../constants';
import get from 'lodash/get';
import { TIME_UNITS } from '../constants';
import { TIMELINE_SCROLL_REFS, BODY } from '../../constants';
const { DATA_TYPES } = gridConstants;
const renderTimelineBody = (state, { dispatch }) => {
	const {
		properties,
		movingTaskInfo,
		drawingLinkData,
		timelinePopover,
		dependencyLinePaths
	} = state;
	const {
		tree,
		dir,
		config,
		timelineData,
		bodyWrapperRef,
		positionMaps: {
			taskPositionMap
		},
		bottomScaleData,
		visibleTasks,
		syncScroll,
		setRef,
		columns
	} = properties;
	const startCol = columns.find(col => col.name === 'startDate'),
		endCol = columns.find(col => col.name === 'endDate');

	const areDateTypeColumns = get(startCol, 'type') === DATA_TYPES.DATE_TYPE && get(endCol, 'type') === DATA_TYPES.DATE_TYPE;
	const totalWidth = get(timelineData, 'totalWidth', 0);
	const {
		canCreateDependency,
		displayDependencyLines,
		rowHeight
	} = config;
	const timeLineBodyRef = createRef();
	const treeUtils = getTreeUtils(tree);
	let top = 0;
	if (movingTaskInfo) {
		top = treeUtils.getNodeById(movingTaskInfo.id).index * rowHeight;
	}

	setRef('timeLineBodyRef', timeLineBodyRef, TIMELINE_SCROLL_REFS);

	return (
		<div
			className="sn-gantt-timeline-body-wrapper"
			ref={timeLineBodyRef}
			on-scroll={e => syncScroll(e, TIMELINE_SCROLL_REFS, BODY)}
		>
			<div
				className="sn-gantt-timeline-body"
				style={{
					width: `${totalWidth}px`,
					minWidth: `${totalWidth}px`
				}}
			>
				{timelineData
					? <Fragment>
						{
							renderVerticalLines({
								timelineData,
								dir,
								totalWidth,
								bottomScaleData
							})
						}
						{
						// For 1st and 2nd tasks, we show tooltip below the bar
						// If end date is being changed, we show tooltip at the end
							movingTaskInfo
								? renderBarTooltip({
									unit: get(timelineData, 'zoomLevel.config.bottom.rangeUnit', TIME_UNITS.DAY),
									precision: get(timelineData, 'precision', TIME_UNITS.MILLISECOND),
									areDateTypeColumns,
									...movingTaskInfo,
									rowHeight,
									scrollTop: bodyWrapperRef.current.scrollTop,
									isRTL: dir === RTL,
									taskRect: taskPositionMap[movingTaskInfo.id],
									totalWidth,
									top: top
								}) : <Fragment />
						}{
							timelinePopover && visibleTasks.find(task => timelinePopover.id === task.id)
								? renderBarHoverTooltip({
									...timelinePopover,
									rowHeight,
									scrollTop: bodyWrapperRef.current.scrollTop
								}) : <Fragment />
						}
						<sn-gantt-timeline-links
							totalWidth={totalWidth}
							dependencyLinePaths={dependencyLinePaths}
							timelineData={timelineData}
							config={config}
							displayDependencyLines={displayDependencyLines}
							className="sn-gantt-timeline-body-links timeline-body-layer"
						/>
						<div
							className="sn-gantt-timeline-body-bars timeline-body-layer"
						>
							{
								renderBars(state, dispatch)
							}
						</div>

						<div
							className="sn-gantt-timeline-body-bars sn-gantt-timeline-body-bars--background timeline-body-layer"
						>
							{
								renderBarsBackground(state)
							}
						</div>
						{
							canCreateDependency ? <div className="timeline-body-layer">
								{
									drawingLinkData ? drawDynamicLink({
										drawingLinkData,
										isRTL: dir === RTL
									}) : <Fragment />
								}
							</div> : <Fragment />
						}
					</Fragment>
					: <Fragment />}
			</div>
		</div>
	);
};

export default renderTimelineBody;
