import ZoomLevel from '../models/ZoomLevel';
import { DEFAULT_ZOOM_LEVELS } from '../default/zoomLevels';

export const getZoomLevels = (scales = []) => {
	return [
		...DEFAULT_ZOOM_LEVELS,
		...scales
	].map(zoom => {
		return new ZoomLevel(zoom);
	});
};

export const findZoomLevel = (zoomLevels, id) => {
	let index = zoomLevels.findIndex(zoom => zoom.id === id);
	if (index !== -1 && zoomLevels[index].disabled) {
		index = zoomLevels.findIndex(zoom => !zoom.disabled);
	}

	return zoomLevels[index];
};
