import { getTreeUtils } from '@devsnc/sn-datagrid';
import { getTaskPositionMap, getTaskPosition } from './TaskManager';
import { getDependencyLines } from './LinkManager';
import { getTaskLayerPositionMap } from './TaskLayerManager';
import { getBaselinePositionMap } from './BaselineManager';

/**
 * @param {Array} tasks - tasks prop
 * @param {Array} links - links prop
 * @param {object} timelineData
 * @param {object} config
 * @param {Tree} tree - tree object
 * All position maps return the respective horizontal positions.
 */
export const getTaskAndLinkPositions = ({
	tasks = [],
	links = [],
	timelineData,
	config,
	tree = {}
}) => {
	const {
		taskLayers,
		canShowBaselines
	} = config;
	const taskPositionMap = getTaskPositionMap({
		tasks,
		timelineData
	});
	const linkPositionMap = getDependencyLines({
		taskPositionMap,
		links,
		tree
	});
	const taskLayerPositionMap = getTaskLayerPositionMap({
		taskLayers,
		timelineData,
		tasks
	});

	let baselinePositionMap = {};

	if (canShowBaselines) {
		baselinePositionMap = getBaselinePositionMap({
			tasks,
			timelineData
		});
	}

	return {
		taskPositionMap,
		linkPositionMap,
		taskLayerPositionMap,
		baselinePositionMap
	};
};

/**
 * Method which updated the position map of a specific tasks and its dependencies
 * Used while moving/dragging a task, to update its position on mousemove
 */
export const getUpdatedPositionMapForTask = ({
	tree,
	task: {
		id,
		startDate,
		endDate,
		links
	},
	taskPositionMap,
	linkPositionMap,
	timelineData
}) => {
	const treeUtils = getTreeUtils(tree);
	const task = treeUtils.getNodeById(id);
	const newTaskPosition = getTaskPosition({
		timelineData,
		startDate,
		endDate,
		type: task.getValue('type')
	});

	// Update the particular task position in the position map
	const newTaskPositionMap = {
		...taskPositionMap,
		[id]: newTaskPosition
	};

	/**
	 * Get the new link positions for the moving task
	 *    inbound and  outbound
	 */
	const newLinkPosition = getDependencyLines({
		links,
		taskPositionMap: newTaskPositionMap,
		tree
	});

	return {
		taskPositionMap: newTaskPositionMap,
		linkPositionMap: {
			...linkPositionMap,
			...newLinkPosition
		}
	};
};
