import '@servicenow/now-dropdown';
import { ZOOM_LEVEL_TITLE } from '../../../../constants/translated';
import get from 'lodash/get';
import { CALENDAR } from '../../../constants';

const renderCalendar = ({ timelineData, possibleZoomLevels }) => {
	return (
		<div className="sn-gantt-timeline-header-calendar">
			<now-dropdown
				className="sn-gantt-timeline-header-calendar-icon"
				bare
				hide-label icon="calendar-outline"
				items={possibleZoomLevels}
				panel-fit-props='{}'
				select="single"
				size="md"
				selected-items={[get(timelineData, 'zoomLevel.id', '')]}
				variant="secondary"
				tooltip-content={ZOOM_LEVEL_TITLE}
				config-aria={{ 'aria-label': ZOOM_LEVEL_TITLE }}
				append-to-payload={{ type: CALENDAR }}
			></now-dropdown>
		</div>
	);
};

export default renderCalendar;
