import Scale from './Scale';
import { ORDERED_TIME_UNITS } from '../constants';

export default class BottomScale extends Scale {
	constructor({
		format,
		rangeUnit,
		step,
		width = 24
	}, {
		rangeUnit: topRangeUnit
	}) {
		super({
			format,
			rangeUnit,
			step
		});
		if (ORDERED_TIME_UNITS.indexOf(topRangeUnit) < ORDERED_TIME_UNITS.indexOf(rangeUnit)) {
			throw new Error('Bottom scale unit cannot be larger than the top scale unit');
		}

		this.width = width;
	}
}
