import { COMPONENT_RENDERED, COMPONENT_PROPERTY_CHANGED, COMPONENT_DOM_READY } from '@servicenow/ui-core/lib/constants';
import { TIMELINE_SECTION, RTL } from '../../../constants';
import { getDisplayScale } from '../../helpers/TimeScaleVirtualization';
import { HEADER_HORIZONTAL_SCROLLED, VIRTUALIZATION_WINDOW_SIZE } from '../constants';

export const timelineHeaderActions = {
	[COMPONENT_PROPERTY_CHANGED]: {
		effect({
			action,
			updateState,
			state,
			dispatch
		}) {
			const { name } = action.payload;
			if (name === 'timelineData') {
				updateState({
					...getDisplayScale({ ...state, scaleCache: {} }, dispatch)
				});
			}
		}
	},
	[COMPONENT_DOM_READY]: ({
		action: {
			payload: {
				host
			}
		},
		updateState,
		state,
		dispatch
	}) => {
		// Firefox: Remembers scroll position causes timeline header & body mismatch issue
		host.scrollTo(0, 0);
		updateState({
			...getDisplayScale({ ...state, scaleCache: {} }, dispatch)
		});
	},
	[COMPONENT_RENDERED]: ({
		action: { payload: { host } },
		properties: { selectedCell }
	}) => {
		if (selectedCell && selectedCell.columnId === TIMELINE_SECTION && selectedCell.rowIndex === 0) {
			host.shadowRoot.querySelector('#sn-gantt-timeline-header').focus();
		}
	},
	[HEADER_HORIZONTAL_SCROLLED]: {
		stopPropagation: true,
		effect({
			action: { payload: { scrollLeft } },
			state,
			properties: { timelineData, dir },
			updateState,
			dispatch
		}) {
			const { scrollPos } = state;
			const { visibleWidth, totalWidth } = timelineData;
			const windowSize = visibleWidth * VIRTUALIZATION_WINDOW_SIZE;
			if (dir === RTL) {
				scrollLeft = totalWidth - scrollLeft - visibleWidth;
			}
			if (scrollLeft < scrollPos - windowSize ||
				scrollLeft > scrollPos + windowSize) {
				updateState({
					scrollPos: scrollLeft,
					...getDisplayScale({ ...state, scrollPos: scrollLeft }, dispatch)
				});
			}
		}
	}
};
