import {
	TIMELINE_EXPAND_REQUESTED,
	LINK_ADDED,
	LINK_CREATED,
	LINK_CLICKED,
	LINK_MOUSEOVER,
	LINK_MOUSEOUT,
	LINK_RIGHT_CLICKED,
	TASK_MOVED,
	TASK_BAR_RIGHT_CLICKED,
	TASK_BAR_CLICKED,
	BOTTOM_SCALE_CHANGED,
	NEW_TASK_ID,
	TASK_RESIZED
} from '../constants';
import { doesDependencyExist, getDependencyLines } from '../timeline/helpers/LinkManager';
import { createLog } from '../../util/logger';
import DateHelper from '../timeline/helpers/DateHelper';
import { getUpdatedTimelineDataAndPositionMaps } from './componentActions';
import {
	TASK_MOVED_INTERNAL,
	TASK_BAR_RIGHT_CLICKED_INTERNAL,
	TASK_BAR_CLICKED_INTERNAL,
	LINK_INTERNAL_EVENT,
	TIMELINE_EXTEND_UNITS,
	TIME_UNITS
} from '../timeline/constants';

const {
	getNativeDiff,
	getNextUnit,
	getISODateTimeFormattedValue,
	getPrevUnit,
	getFormattedValue
} = DateHelper;

const LOG = createLog('actionsLogger');

const commonTimelineLinkActionEffect = ({ state, action: { payload: { eventType, data } }, dispatch }) => {
	const { positionMaps: { linkPositionMap } } = state;
	let eventName;

	switch (eventType) {
		case 'mouseover':
			eventName = LINK_MOUSEOVER;
			break;
		case 'mouseout':
			eventName = LINK_MOUSEOUT;
			break;
		case 'click':
			eventName = LINK_CLICKED;
			break;
		case 'contextmenu':
			eventName = LINK_RIGHT_CLICKED;
			break;
		default:
			break;
	}

	if (eventName) {
		dispatch(eventName, {
			...data,
			...linkPositionMap[data.id].link
		});
	}
};

export const timelineActionHandlers = {
	[LINK_INTERNAL_EVENT]: {
		stopPropagation: true,
		effect: commonTimelineLinkActionEffect
	},
	[TASK_MOVED_INTERNAL]: {
		stopPropagation: true,
		effect({ action: { payload }, dispatch, state }) {
			const { config, timelineData } = state;
			const eventName = payload.direction ? TASK_RESIZED : TASK_MOVED;
			const format = timelineData.precision === TIME_UNITS.DAY
				? config.dateFormat
				: config.dateTimeFormat;

			dispatch(
				eventName,
				{
					...payload,
					newStartDate: {
						value: payload.startDate,
						displayValue: getFormattedValue(payload.startDate, format)
					},
					newEndDate: {
						value: payload.endDate,
						displayValue: getFormattedValue(payload.endDate, format)
					}
				}
			);
		}
	},
	[TASK_BAR_RIGHT_CLICKED_INTERNAL]: {
		stopPropagation: true,
		effect({ action: { payload }, dispatch }) {
			dispatch(TASK_BAR_RIGHT_CLICKED, { ...payload });
		}
	},
	[TASK_BAR_CLICKED_INTERNAL]: {
		stopPropagation: true,
		effect({ action: { payload }, dispatch }) {
			dispatch(TASK_BAR_CLICKED, { ...payload });
		}
	},
	[LINK_ADDED]: {
		stopPropagation: true,
		effect({
			action: {
				payload
			},
			updateState,
			state: {
				properties: {
					links
				},
				positionMaps,
				tree
			},
			dispatch
		}) {
			const {
				taskPositionMap,
				linkPositionMap
			} = positionMaps;
			const newLink = {
				...payload,
				id: NEW_TASK_ID
			};

			// Don't create link if it exists already
			if (!doesDependencyExist(links, newLink)) {
				const newLinkPosition = getDependencyLines({
					links: [
						newLink
					],
					taskPositionMap,
					tree
				});

				updateState({
					positionMaps: {
						...positionMaps,
						linkPositionMap: {
							...linkPositionMap,
							...newLinkPosition
						}
					}
				});

				dispatch(LINK_CREATED, newLink);
			} else {
				LOG.warn('The relationship already exists');
			}
		}
	},
	[TIMELINE_EXPAND_REQUESTED]: {
		stopPropagation: true,
		effect({
			action,
			state,
			updateState
		}) {
			const {
				startDate: timelineStartDate,
				endDate: timelineEndDate
			} = state;
			const {
				startDate: newStartDate,
				endDate: newEndDate
			} = action.payload;
			const {
				zoomLevel: {
					config
				}
			} = state.timelineData;
			const { rangeUnit } = config.bottom;

			const startDiff = Math.round(getNativeDiff(timelineStartDate, newStartDate, rangeUnit));
			const endDiff = Math.round(getNativeDiff(newEndDate, timelineEndDate, rangeUnit));
			const extendStartSide = startDiff < 0;
			const extendEndSide = endDiff < 0;
			if (extendStartSide) {
				const startDate = getISODateTimeFormattedValue(
					getPrevUnit(
						newStartDate,
						rangeUnit,
						TIMELINE_EXTEND_UNITS
					)
				);

				updateState({
					startDate,
					...getUpdatedTimelineDataAndPositionMaps({ ...state, startDate })
				});
			}
			if (extendEndSide) {
				const endDate = getISODateTimeFormattedValue(
					getNextUnit(
						newEndDate,
						rangeUnit,
						TIMELINE_EXTEND_UNITS
					)
				);

				updateState({
					endDate,
					...getUpdatedTimelineDataAndPositionMaps({ ...state, endDate })
				});
			}
		}
	},
	[BOTTOM_SCALE_CHANGED]: {
		stopPropagation: true,
		effect({ action: { payload }, updateState }) {
			updateState({
				...payload
			});
		}
	}
};
