import isEmpty from 'lodash/isEmpty';
import { gridConstants } from '@devsnc/sn-datagrid';
const {
	CELL_BLURRED
} = gridConstants;

export default [{
	events: ['click'],
	effect({
		state: {
			properties: {
				selectedCell
			},
			timelinePopover
		},
		updateState,
		dispatch
	}) {
		if (!isEmpty(selectedCell)) {
			dispatch(CELL_BLURRED);
		} else if (timelinePopover !== null) {
			updateState({ timelinePopover: null });
		}
	}
}];
