import { createRef } from '@servicenow/ui-renderer-snabbdom';
import { INCREASE_ZOOM_LEVEL, DECREASE_ZOOM_LEVEL } from '../../../constants/translated';
import { ZOOM_IN_ZOOM_OUTS } from '../default/zoomLevels';
import { findZoomPercentageIndex } from '../helpers/ZoomPercentageManager';

export default ({ zoomPercentage }) => {
	const currentIndex = findZoomPercentageIndex(zoomPercentage.multiplicationFactor);
	const plusDisabled = ZOOM_IN_ZOOM_OUTS.length - 1 === currentIndex;
	const minusDisabled = currentIndex === 0;
	const zoomInZoomOutRef = createRef();

	return (
		<div ref={zoomInZoomOutRef} className="zoom-container">
			<now-button-iconic icon="minus-fill"
				size="sm"
				tooltip-content={DECREASE_ZOOM_LEVEL}
				append-to-payload={{
					type: 'zoomLevel',
					actionType: 'zoomOut',
					zoomInZoomOutRef
				}}
				disabled={minusDisabled}
				on-click={(e) => {
					e.preventDefault();
				}}
				variant="tertiary">
			</now-button-iconic>
			<span className="zoom-text">{zoomPercentage.text}</span>
			<now-button-iconic icon="plus-fill"
				size="sm"
				tooltip-content={INCREASE_ZOOM_LEVEL}
				append-to-payload={{
					type: 'zoomLevel',
					actionType: 'zoomIn',
					zoomInZoomOutRef
				}}
				disabled={plusDisabled}
				variant="tertiary"
				on-click={(e) => {
					e.preventDefault();
				}}>
			</now-button-iconic>
		</div>
	);
};
